
// site-map
.site-map {
    &__list {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__item {
        padding: 24px 30px;
        border-radius: 20px;
        background: var(--background-green);

        &-head {
            display: block;
            font-size: 18px;
            line-height: 130%;
            color: var(--text-color);
            margin-bottom: 16px;
            transition: all 0.2s ease-in;
            &:hover {
                color: var(--secondary-color);
            }
        }

        & ul {
            display: flex;
            flex-direction: column;
            gap: 10px;
            & a {
                display: block;
                font-weight: 500;
                font-size: 16px;
                line-height: 130%;
                color: var(--text-color);
                opacity: 0.7;
                transition: all 0.2s ease-in;
                &:hover {
                    color: var(--main-color);
                }
            }
        }

    }
}

@media (max-width: 1200px) {
    .site-map {
        &__list {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}
@media (max-width: 900px) {
    .site-map {
        &__list {
            grid-template-columns: repeat(2, 1fr);
        }
    }
}
@media (max-width: 767px) {
    .site-map {
        &__item {
            border-radius: 16px;
            padding: 16px;
    
            &-head {
                font-size: 16px;
            }
    
            & ul {
                & a {
                    font-size: 14px;
                }
            }
    
        }
    }
}
@media (max-width: 600px) {
    .site-map {
        &__list {
            grid-template-columns: repeat(1, 1fr);
        }
    }
}