// pansionat-about

.description__row {
    display: flex;
    align-items: start;
    gap: 40px;
}

@media (max-width: 960px) {
    .description__row .form {
        display: none;
    }
}

.comfort {
    &__swiper-slide {
        height: auto;
    }
    &__card {
        padding: 10px;
        border-radius: 24px;
        background: var(--background-green);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        height: 100%;

        &-wrap {
            padding: 10px 14px;
            font-size: 14px;
            line-height: 120%;
            display: flex;
            flex-direction: column;
            gap: 10px;
        }

        &-txt {
            font-weight: 600;
            font-size: 18px;
            line-height: 120%;
            color: var(--main-color);
        }

        &-img {
            width: 100%;
            border-radius: 16px;
            & img {
                height: 180px;
            }
        }
    }
}

@media (max-width: 560px) {
    .comfort {
        &__card {
            padding: 10px;
            border-radius: 16px;
            gap: 12px;
    
            &-wrap {
                padding: 10px 14px;
            }
            &-txt {
                font-size: 16px;
            }
    
            &-img {
                border-radius: 12px;
                & img {
                    height: 140px;
                }
            }
        }
    }
}

.schedule {
    &__list {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__item {
        padding: 12px;
        border-radius: 20px;
        background: var(--background-green);
        display: flex;
        gap: 24px;

        &-time {
            padding: 12px 20px;
            border-radius: 12px;
            background: var(--main-color);
            font-weight: 600;
            font-size: 20px;
            line-height: 120%;
            text-align: center;
            color: var(--white);
            flex-basis: 88px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            gap: 11px;

            &-separator {
                width: 1px;
                height: 100%;
                background: rgba(255, 255, 255, 0.16);
            }
        }

        &-wrap {
            flex-grow: 1;
        }
        
        &-text {
            padding: 12px 0;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 10px;
            max-width: 540px;
            height: 100%;
            & span {
                font-weight: 600;
                font-size: 20px;
                line-height: 120%;
                color: var(--main-color);
            }
        }

        &-img {
            & img {
                border-radius: 12px;
                max-width: 240px;
            }
        }
    }
}

@media (max-width: 767px) {
    .schedule {
        &__item {
            border-radius: 16px;
            padding: 8px 8px 16px;
            flex-direction: column;
            gap: 16px;
    
            &-time {
                flex-direction: row;
                padding: 15px 20px;
                border-radius: 10px;
                font-size: 16px;
                flex-basis: 100%;
                &-separator {
                    display: none;
                }
            }
    
            &-wrap {
                flex-grow: 1;
            }
            
            &-text {
                padding: 0 8px;
                gap: 12px;
                max-width: none;
                & span {
                    font-size: 18px;
                }
            }
    
            &-img {
                display: none;
            }
        }
    }
}


.rooms {
    &__swiper-slide {
        height: auto;
    }
    &__card {
        padding: 10px;
        border-radius: 30px;
        background: var(--background-green);
        height: 100%;
        display: flex;
        flex-direction: column;

        &-img {
            margin-bottom: 22px;
            & img {
                border-radius: 24px; 
                max-height: 210px;
            }
        }

        &-text {
            padding: 0 20px 17px;
            flex-grow: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 30px;
        }

        &-name {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 130%;
            letter-spacing: -0.01em;
            color: var(--main-color);
            margin-bottom: 16px;
        }

        &-price {
            border-radius: 8px;
            padding: 12px;
            background: var(--white);
            font-weight: 600;
            font-size: 18px;
            line-height: 120%;
            color: var(--main-color);
            margin-bottom: 24px;
            width: fit-content;
        }

        &-list {
            & ul {
                max-height: 240px;
                overflow: hidden;
                margin-bottom: 16px;
                transition: all 0.3s ease;
            }
            & li {
                font-size: 16px;
                &:not(:last-child) {
                    padding-bottom: 12px;
                    border-bottom: 1px solid var(--line-green);
                    margin-bottom: 12px;
                }
            }
        }

        &-more {
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            text-decoration: underline;
            text-decoration-skip-ink: none;
            color: var(--main-color);
        }
    }

    &__swiper-pagination {
        display: block;
    }
}

@media (max-width: 767px) {
    .rooms {
        &__swiper {
            overflow: visible;
        }
        &__card {
            padding: 8px;
            border-radius: 16px;
    
            &-img {
                margin-bottom: 16px;
                & img {
                    border-radius: 12px; 
                    max-height: 160px;
                }
            }
    
            &-text {
                padding: 0 8px 8px;
                gap: 20px;
            }
    
            &-name {
                font-size: 18px;
                margin-bottom: 12px;
            }
    
            &-price {
                padding: 10px 12px;
                font-size: 16px;
                margin-bottom: 20px;
            }
    
            &-list {
                & ul {
                    max-height: 240px;
                    margin-bottom: 10px;
                }
                & li {
                    font-size: 14px;
                    &:not(:last-child) {
                        padding-bottom: 10px;
                        margin-bottom: 10px;
                    }
                }
            }
    
            &-more {
                font-size: 14px;
            }
        }
    }
}

.pansionat-categories {
    &__title {
        margin-bottom: 40px;
    }
    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 12px;
    }
}

@media (max-width: 767px) {
    .pansionat-categories {
        &__title {
            margin-bottom: 20px;
        }
        &__list {
            gap: 8px;
        }
    }
}