/* popups */
.popup {
    height: 100vh;
    left: 50%;
    opacity: 0;
    pointer-events: none;
    position: fixed;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
    width: 100%;
    z-index: 1000;
    transition: opacity 0.3s ease;
    overflow-y: auto;
    background: rgba(0, 0, 0, 0.7);
}
.popup.open {
    opacity: 1;
    pointer-events: all;
}

.popup__body {
    width: 100%;
    min-height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    padding: 20px;
}


.popup__content {
    position: relative;
    width: 100%;
    position: relative;
    transition: all 0.3s linear;
    opacity: 0;
    max-width: 892px;
}
.popup.open .popup__content {
    opacity: 1;
}

.close-popup {
    cursor: pointer;
    position: absolute;
    top: 24px;
    right: 24px;
    z-index: 1000;
    width: 14px;
    height: 14px;
    transition: all 0.4s linear;
    & img {
        height: 100%;
    }
    &:hover {
        transform: scale(1.1);
    }
}

.none {
    display: none;
}

@media (max-width: 810px) {
    .popup__content {
        max-width: 480px;
    }
    .close-popup {
        top: 16px;
        right: 16px;
        width: 12px;
        height: 12px;
    }
}
