
/* Общие glob */

html.lock {
    overflow: hidden;
}
html.burger-lock {
    overflow: hidden;
}
html.burger-lock::before, html.lock::before {
    opacity: 1;
    pointer-events: all;
}
html::before {
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3215686275);
    position: fixed;
    inset: 0;
    opacity: 0;
    z-index: 4;
    pointer-events: none;
    -webkit-transition: opacity 0.3s linear;
    -o-transition: opacity 0.3s linear;
    transition: opacity 0.3s linear;
}
.no-scroll {
    overflow: hidden;
}
body.lock {
    overflow: hidden;
}
body.burger-lock {
    overflow: hidden;
}

body {
    margin: 0;
    //overflow-x: hidden;
    overflow-y: auto;
    padding: 0;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    color: rgba(45, 49, 48, 0.8);
    background: var(--white);
}
.container {
	margin: 0 auto;
	padding: 0 var(--container-padding);
	max-width: var(--container-width);
	width: 100%;
}
@media (max-width: 1720px) {
    .container {
        max-width: 1600px;
    }
}

.page__top {
    margin-bottom: 50px;
}

.section-offset {
    margin-bottom: 160px;
    overflow: hidden;
}
.section-offset-mini {
    margin-bottom: 100px;
}

.section__title {
    margin-bottom: 40px;
}

.section__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 40px;
}

.green-section {
    margin-right: 20px;
    margin-left: 20px;
    background: var(--background-green);
    border-radius: 40px;
    position: relative;
    overflow: hidden;
}



/*  buttons */
.btn-green, .btn-orange, .btn-white {
    display: block;
    border-radius: 16px;
    padding: 23px 40px;
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    text-align: center;
    transition: all 0.3s ease;

    &:disabled {
        opacity: 0.6;
        pointer-events: none;
    }
}
.btn-small {
    border-radius: 12px;
    padding: 16px 23px;
    font-size: 18px;
}

.btn-green {
    background: var(--main-color);
    color: var(--white);
    &:hover {
        background: var(--secondary-color);
    }
}

.btn-orange {
    background: var(--secondary-color);
    color: var(--white);
    &:hover {
        background: var(--main-color);
    }
}

.btn-white {
    background: var(--white);
    color: var(--main-color);
    border: 1px solid var(--white);
    &:hover {
        color: var(--white);
        background: var(--main-color);
    }
}


.btn-more, .btn-green-text {
    display: block;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    text-decoration: underline !important;
    color: var(--main-color);
    transition: all 0.3s ease;
    &:hover {
        color: var(--text-green);
    }
}


.links {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}
.link {
    display: block;
    border-radius: 12px;
    padding: 16px 20px 16px 46px;
    background: #f2faf6;
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    color: var(--text-color);
    position: relative;
    transition: all 0.3s ease;

    &::after {
        content: "";
        display: block;
        width: 10px;
        height: 10px;
        border-radius: 100%;
        background: var(--main-color);
        position: absolute;
        top: 25px;
        left: 20px;
        transition: all 0.3s ease;
    }
    &:hover, &.active {
        background: var(--main-color);
        color: var(--white);
        &::after {
            background: var(--white);
        }
    }
}



/*  titles  */

.title-h1, .title-h2, .title-h3 {
    font-family: var(--second-family);
    font-weight: 600;
    line-height: 120%;
    letter-spacing: -0.01em;
    color: var(--text-color);
    & span {
        color: var(--secondary-color);
    }
}
.title-h1 {
    font-size: 54px;
}
.title-h2 {
    font-size: 46px;
}
.title-h3 {
    font-size: 32px;
}
.title-br span {
    display: block;
}


.img-cover {
    display: block;
    overflow: hidden;
    & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}



/*  form  */

.input, .textarea {
    border-radius: 16px;
    padding: 23px 24px;
    background: rgba(255, 255, 255, 0.2);
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;
    color: rgba(255, 255, 255, 1);
    border: 1px solid transparent;
    width: 100%;
    transition: all 0.3s ease;

    &::placeholder {
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        color: rgba(255, 255, 255, 0.6);
    }
    &:hover,
    &:focus,
    &:active {
        border: 1px solid rgba(255, 255, 255, 0.5);
        background: rgba(255, 255, 255, 0.1);
    }
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	-webkit-appearance:none;
}

.textarea {
    min-height: 142px;
    resize: none;
}

.politic {
    display: flex;
    gap: 12px;
    align-items: center;

    & input[type="checkbox"] {
        position: relative;
        flex-shrink: 0;
        border: 1px solid var(--line-green);
        border-radius: 4px;
        width: 20px;
        height: 20px;
        color: #000;
        appearance: none;
        outline: 0;
        cursor: pointer;
        transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
        &::before {
            position: absolute;
            content: '';
            display: block;
            top: 3px;
            left: 7px;
            width: 5px;
            height: 9px;
            border-style: solid;
            border-color: var(--main-color);
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
            opacity: 0;
        }
        &:checked {
            color: #fff;
            border-color: var(--white);
            background: var(--white);
            &::before {
                opacity: 1;
            }
        }
    }
    & label {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 130%;
        color: rgba(255, 255, 255, 0.7);
        & a {
            color: inherit;
            text-decoration: underline;
            text-decoration-skip-ink: none;
            transition: all 0.3s ease;
            &:hover {
                opacity: 0.5;
            }
        }
        &::before {
            position: absolute;
            content: attr(data-content);
        }
    }
}


/*  search  */
.form-search {
    position: relative;
    width: 100%;
    & input {
        background: var(--fon-plashek);
        opacity: 0.7;
        width: 100%;
        &:focus {
            opacity: 1;
        }
    }
    & img {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }
}

/* select */
.select {
    cursor: pointer;
    outline: none;
    border-radius: 8px;
    padding: 12px 16px;
    background: var(--background-green);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    color: var(--text-color);
    padding-right: 30px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    background: var(--background-green) url(./../img/icons/arrow-bottom.svg) no-repeat right;
    background-position-x: calc(100% - 16px);
    transition: all 0.3s ease;
}



.sort-checkbox {
    display: flex;
    gap: 12px;
    align-items: center;

    & input[type="checkbox"] {
        position: relative;
        flex-shrink: 0;
        border: 1px solid var(--line-green);
        border-radius: 4px;
        width: 20px;
        height: 20px;
        color: #000;
        appearance: none;
        outline: 0;
        cursor: pointer;
        transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
        &::before {
            position: absolute;
            content: '';
            display: block;
            top: 3px;
            left: 7px;
            width: 5px;
            height: 9px;
            border-style: solid;
            border-color: #fff;
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
            opacity: 0;
        }
        &:checked {
            color: #fff;
            border-color: var(--main-color);
            background: var(--main-color);
            &::before {
                opacity: 1;
            }
        }
    }
    & label {
        position: relative;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 16px;
        line-height: 130%;
        color: var(--text-color);
        &::before {
            position: absolute;
            content: attr(data-content);
        }
    }

    &.white {
        & input[type="checkbox"] {
            border: 1px solid var(--white);
            &::before {
                border-color: var(--main-color);
            }
            &:checked {
                color: var(--main-color);
                border-color: var(--white);
                background: var(--white);
            }
        }
        & label {
            color: var(--white);
        }
    }
}

.input-radio {
    display: flex;
    gap: 12px;
    align-items: center;

    & input[type="radio"] {
        position: relative;
        flex-shrink: 0;
        border: 1px solid var(--text-color);
        outline: none;
        opacity: 0.5;
        border-radius: 12px;
        height: 20px;
        width: 20px;
        color: #000;
        appearance: none;
        cursor: pointer;
        transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
        &::before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%) scale(0);
            width: 12px;
            height: 12px;
            background: transparent;
            border-radius: 50%;
            pointer-events: none;
            transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
        }
    
        &:checked {
            opacity: 1;
            border-color: var(--main-color);
            &::before {
                background: var(--main-color);
                transform: translate(-50%, -50%) scale(1);
                opacity: 1;
            }
        }
    }
    & label {
        position: relative;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        color: var(--text-color);
        opacity: 0.7;
        &::before {
            position: absolute;
            content: attr(data-content);
            color: var(--main-color);
            opacity: 0;
        }
    }

    &.white {
        & input[type="radio"] {
            border: 1px solid var(--white);
            &:checked {
                border-color: var(--white);
                &::before {
                    background: var(--white);
                }
            }
        }
        & label {
            color: var(--white);
        }
    }
}




/* range */
input[type="range"] {
    -webkit-appearance: none; 
    appearance: none;
    width: 100%; 
    height: 4px; 
    background: var(--color-svetlo-zelenyy-1);
    border-radius: 20px;
    outline: none; 
    -webkit-transition: .2s;
    transition: opacity .2s;
    &::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        width: 17px;
        height: 17px;
        border-radius: 100%; 
        background: var(--color-zelenyy);
        cursor: pointer;
    }
    &::-moz-range-thumb {
        width: 17px;
        height: 17px;
        border-radius: 100%; 
        background: var(--color-zelenyy);
        cursor: pointer;
    }
    &:focus::-webkit-slider-thumb {
        outline: 2px solid var(--color-zelenyy);
        outline-offset: 0.125rem;
    }
    &:focus::-moz-range-thumb {
        outline: 2px solid var(--color-zelenyy);
        outline-offset: 0.125rem;
    }
    &:focus::-webkit-slider-thumb {
        outline: 2px solid var(--color-zelenyy);
        outline-offset: 0.125rem;
    }
}




/* accordion */
.accordion {
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    cursor: pointer;
    width: 100%;
    position: relative;

    &--active {
        .accordion__btn-more {
            transform: rotate(180deg);
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        -webkit-transition: all 0.4s ease;
        -o-transition: all 0.4s ease;
        transition: all 0.4s ease;
    }
}

/* tabs */
.tab__btns-select {
    border: 1px solid var(--line-green);
    border-radius: 16px;
    padding: 10px;
    display: flex;
    gap: 10px;
    margin-bottom: 40px;
}
.tab__btn-select {
    flex-grow: 1;
    border-radius: 12px;
    padding: 12px 16px;
    background: var(--background-green);
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    color: var(--text-color);
    transition: all 0.3s ease;
    &:hover, &.active {
        background: var(--green-200);
    }
}
.tab-content {
    display: none;
    &.active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}

@media (max-width: 767px) {
    .tab-select {
        display: flex;
        flex-direction: column;
        gap: 8px;

        & .tab-content {
            padding: 16px 0;
        }
    }
    .tab__btns-select {
        display: contents;
    }
    .tab__btn-select {
        width: 100%;
        padding: 16px;
        display: flex;
        justify-content: space-between;
        gap: 10px;
        text-align: left;
        &::after {
            content: "";
            display: block;
            width: 8px;
            height: 8px;
            border-bottom: 1.5px solid var(--text-color);
            border-right: 1.5px solid var(--text-color);
            transform: rotate(45deg);
            position: relative;
            top: 5px;
            flex-shrink: 0;
            transition: all 0.2s ease-in;
        }
        &.active::after {
            transform: rotate(225deg);
            top: 7px;
        }
    }
}



/* rating */
.full-stars {
    text-align: center;
}
.full-stars .rating-group {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
}
.full-stars input {
    position: absolute;
    left: -9999px;
}
.full-stars label {
    margin: 0;
    cursor: pointer;
}
.full-stars label svg {
    margin: 0 2px;
    height: 18px;
    width: 20px;
    fill: var(--color-temno-zelenyy);
    -webkit-transition: fill 0.3s;
    -o-transition: fill 0.3s;
    transition: fill 0.3s;
}
.full-stars input:checked ~ label svg {
    fill: transparent;
}
.full-stars .rating-group:hover label svg {
    fill: var(--color-temno-zelenyy);
}
.full-stars .rating-group input:hover ~ label svg {
    fill: transparent;
}


/* breadcrumbs */
.breadcrumbs{
    margin-bottom: 50px;
}
.breadcrumbs, .breadcrumbs li {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    gap: 10px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.breadcrumbs li, .breadcrumbs li a {
    font-size: 14px;
    color: rgba(45, 49, 48, 0.6);
}
.breadcrumbs li a {
    color: var(--text-color);
}
.breadcrumbs li a:hover {
    opacity: 0.8;
}
.breadcrumbs li:after {
    content: "—";
    display: block;
    font-size: 20px;
    color: var(--text-color);
    font-size: inherit;
}
.breadcrumbs li:last-child::after {
    content: none;
}

.networks {
    display: flex;
    gap: 10px;
}

.network {
    border-radius: 500px;
    width: 40px;
    height: 40px;
    background: var(--main-color);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
    &:hover {
        background: var(--secondary-color);
    }
}


