    }
}
@media (max-width: 1220px) {
    .section-offset {
        margin-bottom: 100px;
    }
    body {
        font-size: 16px;
    }
    .title-h1 {
        font-size: 38px;
    }
    .title-h2 {
        font-size: 32px;
    }
