        margin-bottom: 24px;
    }
    .section__top {
        flex-direction: column;
        align-items: stretch;
        gap: 12px;
        margin-bottom: 20px;
    }
    body {
        font-size: 14px;
    }
    .green-section {
        margin-right: 10px;
        margin-left: 10px;
