    }
    .title-h2 {
        font-size: 20px;
    }
    .title-h3 {
        font-size: 18px;
    }
    .title-br span {
        display: inline-block;
    }

    .btn-green, .btn-orange, .btn-white {
        border-radius: 12px;
        padding: 17px 24px;
        font-size: 16px;
    }
    .btn-small {
        border-radius: 12px;
        padding: 16px 23px;
        font-size: 16px;
    }

    .btn-more {
        font-size: 14px;
    }

    .link {
        display: block;
        border-radius: 8px;
        padding: 12px 14px 12px 31px;
        font-size: 14px;
    
        &::after {
            width: 7px;
            height: 7px;
            top: 18px;
            left: 14px;
        }
    }

