
// partners
.partners {
    &__cards {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        gap: 20px;
    }

    &__card {
        padding: 20px;
        border-radius: 24px;
        border: 1px solid var(--line-green);
        height: 220px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media (max-width: 1300px) {
    .partners {
        &__cards {
            grid-template-columns: repeat(4, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 1024px) {
    .partners {
        &__cards {
            grid-template-columns: repeat(3, 1fr);
            gap: 12px;
        }
        &__card {
            height: 180px;
            & img {
                max-height: 42px;
            }
        }
    }
}
@media (max-width: 700px) {
    .partners {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 8px;
        }
        &__card {
            padding: 30px;
            border-radius: 10px;
            height: 156px;
            & img {
                max-height: 36px;
            }
        }
    }
}