// category
.category {
    &__row {
        display: flex;
        gap: 40px;
    }

    &__left {
        width: 100%;
        max-width: 392px;
    }

    &__filter {
        border: 1px solid var(--line-green);
        border-radius: 24px;
        padding: 30px;
        margin-bottom: 40px;

        &-wrap {
            display: flex;
            flex-direction: column;
            gap: 30px;
        }

        &-title {
            display: none;
        }

        &-head {
            display: flex;
            justify-content: space-between;
            gap: 10px;
            font-weight: 600;
            font-size: 18px;
            line-height: 120%;
            color: var(--text-color);

            &::after {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                border-bottom: 1.5px solid var(--text-color);
                border-right: 1.5px solid var(--text-color);
                opacity: 0.5;
                transform: rotate(45deg);
                position: relative;
                top: 2px;
                flex-shrink: 0;
                transition: all 0.3s ease;
            }

            &.accordion--active {
                margin-bottom: 20px;
                &::after {
                    opacity: 1;
                    transform: rotate(225deg);
                    position: relative;
                    top: 2px;
                }
            }
        }

        &-content {
            display: flex;
            flex-direction: column;
            gap: 16px;
        }

        &-btns {
            display: flex;
            flex-direction: column;
            gap: 30px;
        }

        &-btn {
            background: rgba(80, 174, 163, 0.1);
        }

        &-btnClear {
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            text-align: center;
            color: var(--text-color);
            opacity: 0.5;
        }
    }

    &__form {
        border-radius: 24px;
        padding: 26px 30px 30px 30px;
        background: var(--main-color);
        min-width: auto;

        &-title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 120%;
            letter-spacing: -0.01em;
            color: var(--white);
            margin-bottom: 16px;
        }

        &-subtitle {
            font-weight: 500;
            font-size: 18px;
            line-height: 130%;
            color: var(--white);
            opacity: 0.8;
            margin-bottom: 50px;
        }

        &-inner {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
    }

    &__right {
        max-width: calc(100% - 392px - 40px);
    }

    &__links {
        display: flex;
        flex-wrap: wrap;
        gap: 12px;
        margin-bottom: 30px;
    }
    
    &__link {
        &-more {
            padding: 16px 20px;
            background: var(--background-grey);
            color: var(--secondary-color);
            &::after {
                content: none;
            }
            &:hover, .active {
                background: var(--secondary-color);
            }
        }
    }

    &__sort {
        border: 1px solid var(--line-green);
        border-radius: 16px;
        padding: 16px 16px 16px 28px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 30px;

        &-text {
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: var(--text-color);
            opacity: 0.7;
        }

        &-selects {
            display: flex;
            gap: 12px;
        }

        &-btn {
            display: none;
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__card {
        border-radius: 24px;
        padding: 15px 15px 30px 15px;
        background: var(--background-green);
        display: flex;
        flex-direction: column;
        height: 100%;

        &-row {
            display: flex;
            gap: 30px;
            margin-bottom: 24px;
            flex-grow: 1;
        }

        &-top {
            border-radius: 16px;
            width: 100%;
            height: 210px;
            margin-bottom: 20px;
            position: relative;
        }

        &-img {
            width: 100%;
            height: 100%;
            border-radius: 16px;
        }

        &-tags {
            display: flex;
            flex-wrap: wrap;
            gap: 6px;
            position: absolute;
            top: 12px;
            left: 12px;
            right: 12px;
        }

        &-tag {
            border-radius: 8px;
            padding: 10px 12px;
            background: var(--white);
            display: flex;
            align-items: center;
            gap: 10px;
            font-weight: 600;
            font-size: 14px;
            line-height: 130%;
            color: var(--text-color);
            &::before {
                content: "";
                display: block;
                width: 16px;
                height: 16px;
                background: url("./../img/icons/tags/tag-1.svg") no-repeat center / contain;
            }
            &[data-name="Самая доступная цена"]::before {
                background: url("./../img/icons/tags/tag-2.svg") no-repeat center / contain;
            }
            &[data-name="Выбор родственников"]::before {
                background: url("./../img/icons/tags/tag-3.svg") no-repeat center / contain;
            }
            &[data-name="Одобрено врачами"]::before {
                background: url("./../img/icons/tags/tag-4.svg") no-repeat center / contain;
            }
        }

        &-main {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 0 10px;
            flex-grow: 1;
        }

        &-name {
            font-weight: 600;
            font-size: 20px;
            line-height: 130%;
            color: var(--text-color);
            margin-bottom: 16px;
        }

        &-location {
            border: 1px solid var(--line-green);
            border-radius: 12px;
            padding: 16px;
            font-weight: 500;
            font-size: 14px;
            line-height: 120%;
            color: rgba(45, 49, 48, 0.7);
            display: flex;
            gap: 12px;
            margin-bottom: 20px;
            &::before {
                content: "";
                display: block;
                width: 14px;
                height: 16px;
                background: url("./../img/icons/location.svg") no-repeat center / contain;
                flex-shrink: 0;
            }
        }

        &-prices {
            display: flex;
            gap: 16px;
        }

        &-price {
            flex-grow: 1;
            font-weight: 600;
            line-height: 120%;
            color: var(--secondary-color);
            margin-bottom: 24px;
            &:first-child {
                padding-right: 16px;
                border-right: 1px solid var(--line-orange);
            }
            & p {
                font-size: 18px;
                margin-bottom: 6px;
            }
            & span {
                font-size: 16px;
                opacity: 0.7;
                text-decoration: line-through;
            }
        }

        &-items {
            display: flex;
            gap: 7px;
            flex-wrap: wrap;
        }

        &-item {
            flex: 1 0 40%;
            border-radius: 10px;
            padding: 10px 12px;
            background: var(--white);
            font-weight: 500;
            font-size: 14px;
            line-height: 120%;
            color: rgba(45, 49, 48, 0.6);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 16px;
            & span {
                display: block;
                font-weight: 600;
                color: var(--text-color);
                margin-top: 4px;
            }
        }

        &-btn {
            display: none;
            background: rgba(80, 174, 163, 0.1);
            &.show {
                display: block;
            }
        }

        &-btns {
            display: flex;
            gap: 10px;
            & > * {
                flex-grow: 1;
            }
        }

        &--pansionats {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            & .category__card-top {
                height: auto;
                margin-bottom: 0;
            }
            & .category__card-tags {
                justify-content: flex-end;
            }
            & .category__card-items {
                margin-bottom: 24px;
            }
            & .category__card-item {
                flex: 1 0 20%;
            }
        }
    }
}

@media (max-width: 1600px) {
    .category {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 14px;
        }
    }
}
@media (max-width: 1500px) {
    .category {
        &__card {
            &--pansionats {
                & .category__card-item {
                    flex: 1 0 40%;
                }
            }
        }
    }
}
@media (max-width: 1420px) {
    .category {
        &__card {
            &--pansionats {
                height: 100%;
                & .category__card-row {
                    flex-direction: column-reverse;
                    gap: 14px;
                    margin-bottom: 0;
                }
                & .category__card-top {
                    height: 220px;
                }
                & .category__card-main {
                    gap: 0;
                }
                & .category__card-btns button:last-child {
                    display: none;
                }
            }
        }
    }
}
@media (max-width: 1200px) {
    .category {
        &__row {
            display: flex;
            gap: 40px;
        }
    
        &__left {
            display: contents;
            max-width: 0;
        }

        &__right {
            max-width: 100%;
        }
    
        &__filter {
            border: none;
            border-radius: 20px 20px 0 0;
            padding: 0;
            background: var(--white);
            margin-bottom: 0;
            height: 100%;
            max-height: 0;
            position: fixed;
            bottom: -40px;
            left: 0;
            right: 0;
            z-index: 5;
            transition: all 0.3s ease;
            
            &.active {
                max-height: 80vh;
                bottom: 0;
            }
            
            &::before {
                content: "";
                display: block;
                border-radius: 4px;
                width: 46px;
                height: 4px;
                background: var(--white);
                position: absolute;
                top: -10px;
                left: 50%;
                transform: translate(-50%, 0);
            }

            &-wrap {
                max-height: 100%;
                overflow-y: auto;
                overflow-x: hidden;
                gap: 24px;
                padding: 16px 16px 20px;
            }
            
            &-title {
                display: block;
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 20px;
                line-height: 130%;
                letter-spacing: -0.01em;
                color: var(--text-color);
            }
    
            &-head {
                font-size: 14px;
    
                &::after {
                    width: 8px;
                    height: 8px;
                    top: 2px;
                }
    
                &.accordion--active {
                    margin-bottom: 10px;
                }
            }
    
            &-content {
                gap: 8px;
            }
    
            &-btns {
                gap: 20px;
            }

            &-btnClear {
                font-size: 14px;
            }
        }
    
        &__form {
            display: none;
        }
    


        &__links {
            gap: 8px;
            margin-bottom: 24px;
        }
        &__link {
            &-more {
                padding: 12px 14px;
            }
        }
    
        &__sort {
            border: none;
            border-radius: 0;
            padding: 0;
            align-items: stretch;
            gap: 6px;
            max-width: calc(100vw - 20px);
            overflow-x: auto;
            margin-bottom: 24px;
    
            &-text {
                display: none;
            }
    
            &-selects {
                gap: 6px;
            }

            &-btn {
                border-radius: 8px;
                padding: 14px 12px;
                background: var(--background-green);
                display: flex;
                align-items: center;
                justify-content: center;
                min-width: 43px;
            }
        }
    
        &__cards {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    
        &__card {
            border-radius: 16px;
            padding: 8px 8px 16px 8px;
    
            &-top {
                border-radius: 12px;
                height: 180px;
                margin-bottom: 16px;
            }
            &-img {
                border-radius: 12px;
            }
    
            &-tags {
                top: 6px;
                left: 6px;
                right: 6px;
            }
    
            &-tag {
                border-radius: 6px;
                padding: 8px 10px;
                gap: 8px;
                font-size: 12px;
                &::before {
                    width: 12px;
                    height: 12px;
                }
            }
    
            &-main {
                padding: 0 8px;
            }
    
            &-name {
                font-size: 16px;
                margin-bottom: 14px;
            }
    
            &-location {
                border-radius: 12px;
                padding: 12px;
                font-size: 12px;
                gap: 8px;
                margin-bottom: 16px;
            }
    
            &-prices {
                display: flex;
                gap: 16px;
            }
    
            &-price {
                display: flex;
                gap: 8px;
                margin-bottom: 16px;
                &:first-child {
                    padding-right: 0;
                    border-right: none;
                }
                &:last-child {
                    display: none;
                }
                & p {
                    font-size: 16px;
                    margin-bottom: 0;
                }
                & span {
                    font-size: 14px;
                    text-decoration: line-through;
                }
            }
    
            &-items {
                display: none;
            }

            &-btn {
                display: block;
            }
        }
    }
}
@media (max-width: 700px) {
    .category {
        &__card--pansionats {
            display: flex !important;
        }
    }
}
@media (max-width: 650px) {
    .category {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
        &__card--pansionats {
            & .category__card-top {
                height: 160px;
            }
        }
    }
}