
// pansionat-review
.pansionat-review {
    &__row {
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }

    &__left  {
        max-width: 50%;
        position: relative;
    }

    &__swiper-btns {
        position: absolute;
        right: 40px;
        bottom: 40px;
    }

    &__swiper-slide {
        height: auto;
    }

    &__card {
        border-radius: 30px;
        padding: 36px 40px 40px;
        background: var(--background-green);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
        font-size: 18px;
        height: 100%;

        &-name {
            font-weight: 500;
            font-size: 20px;
            line-height: 140%;
            color: var(--text-color);
            margin-bottom: 20px;
        }

        &-text {
            max-height: 220px;
            overflow: hidden;
            margin-bottom: 10px;
        }

        &-date {
            border-radius: 12px;
            padding: 20px;
            background: var(--white);
            max-width: 50%;
            display: flex;
            justify-content: space-between;
            gap: 10px;

            & > * {
                font-size: 16px;
                line-height: 130%;
                opacity: 0.6;
            }
        }
    }
}

@media (max-width: 1100px) {
    .pansionat-review {
        &__row {
            flex-direction: column;
            gap: 40px;
        }
        &__left  {
            max-width: 100%;
        }
    }
}
@media (max-width: 767px) {
    .pansionat-review {
        &__card {
            border-radius: 16px;
            padding: 16px 8px 8px;
            gap: 24px;
            font-size: 14px;
            &-wrap {
                padding: 0 8px;
            }
    
            &-name {
                font-size: 16px;
                margin-bottom: 6px;
            }
    
            &-text {
                max-height: 294px;
            }
    
            &-date {
                border-radius: 10px;
                padding: 12px 16px;
                max-width: 100%;
                & > * {
                    font-size: 14px;
                }
            }
        }
    }
}