/* header  */

.header {
    // position: fixed;
    // top: 0;
    // left: 0;
    // right: 0;
    width: 100%;
    z-index: 100;
    background-color: #fff;
    padding-bottom: 20px;
}


.header__logo {
    display: block;
    overflow: hidden;
    max-width: 205px;
    min-width: 136px;
    width: 100%;
}

.logo__picture {
    width: 100%;
    height: 100%;
    & img {
        width: 100%;
        height: 100%;
    }
}

.header__plashka, .header__main, .header__top {
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
}


/* header__top  */

.header__top {
    padding: 20px 0 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
    border-bottom: 1px solid var(--line);
    transition: all 0.3s ease;
}



.header__nav-list {
    display: flex;
    gap: 40px;
}
.header__nav-item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
    opacity: 0.7;
    padding-right: 10px;
}
.header__nav-item a {
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    color: var(--text-color);
}

.header__nav-item .header__sublist, .header__nav-item .header__subsublist {
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 100%;
    left: 0px;
    z-index: 3;
    border-radius: 12px;
    box-shadow: 1px 0 3px 0 rgba(0, 0, 0, 0.03), 5px 0 5px 0 rgba(0, 0, 0, 0.03), -3px 0 6px 0 rgba(0, 0, 0, 0.02), 19px 0 8px 0 rgba(0, 0, 0, 0), 30px 0 8px 0 rgba(0, 0, 0, 0);
    background: var(--white);
    min-width: 350px;
    transition: all 0.3s ease;
}
.header__nav-item .header__subsublist {
    top: 0;
    left: 100%;
}

.header__nav-item:hover {
    opacity: 1;
}
.header__nav-item:hover > .header__sublist, .header__nav-item:hover > .header__subsublist  {
    opacity: 1;
    pointer-events: all;
}

.header__nav-item:hover > span {
    transform: rotate(180deg);
}
.header__sublist .header__nav-item span {
    transform: rotate(-90deg);
}

.header__sublist .header__nav-item {
    transition: all 0.2s ease;
    &:hover {
        background: var(--background-green);
    }
    &:first-child {
        border-radius: 12px 12px 0 0;
    }
    &:last-child {
        border-radius: 0 0 12px 12px;
    }
    & a {
        display: block;
        width: 100%;
        padding: 12px 20px;
        padding: 10px 40px 10px 10px;
    }
}


.header__phone {
    display: flex;
    align-items: center;
    gap: 12px;
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    color: var(--main-color);

    &-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 36px;
        height: 36px;
        border-radius: 20px;
        background: var(--background-green);
    }
}

.header__top-text {
    font-size: 18px;
    opacity: 0.6;
    text-align: right;
}



/* header__midle */
.header__midle {
    padding: 12px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 26px;
}

.header__midle-left {
    display: flex;
    align-items: center;
    gap: 40px;
}

.header__midle-text {
    font-weight: 600;
    font-size: 20px;
    line-height: 120%;
    color: var(--text-green);
}

.header__btns {
    display: flex;
    gap: 10px;
}
.header__btns > button, .header__btns > a {
    min-width: 260px;
}

.header__services-list {
    border-radius: 16px;
    background: var(--main-color);
    padding: 10px;
    display: flex;
    gap: 8px;
}
.header__services-list a {
    display: flex;
    align-items: center;
    border-radius: 12px;
    padding: 10px 20px;
    background: rgba(255, 255, 255, 0.1);
    font-weight: 600;
    font-size: 16px;
    line-height: 120%;
    color: var(--white);
    height: 100%;
    transition: all 0.2s ease-in;
    &:hover {
        background: rgba(255, 255, 255, 0.28);
    }
}

/* mobile__wrapper */
.header__mobile, .header__menu-mobile {
    display: none;
}



/* burger__menu */
.burger__menu {
    width: 36px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--white);
    border-radius: 6px;
}
.ham {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: transform 400ms;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.hamRotate.active {
    transform: rotate(45deg);
}
.hamRotate180.active {
    transform: rotate(180deg);
}
.line {
    fill:none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke:var(--secondary-color);
    stroke-width:5.5;
    stroke-linecap:round;
}
.ham4 .top {
    stroke-dasharray: 40 121;
}
.ham4 .bottom {
    stroke-dasharray: 40 121;
}
.ham4.active .top {
    stroke-dashoffset: -68px;
}
.ham4.active .bottom {
    stroke-dashoffset: -68px;
}


// media header-1
@media (max-width: 1720px) {
    .header__nav-list {
        gap: 24px;
    }
    .header__services-list a {
        padding: 10px 16px;
        font-size: 14px;
    }
}

@media (max-width: 1520px) {
    .header__top-text {
        display: none;
    }
    .header__midle-text {
        font-size: 16px;
    }
}

@media (max-width: 1400px) {
    .header__logo {
        max-width: 147px;
        min-width: 140px;
    }

    .header__bottom, .header__top-text {
        display: none;
    }

    .header__nav-list {
        gap: 22px;
    }
    .header__nav-item a {
        font-size: 14px;
    }

    .header__midle {
        gap: 20px;
    }
    .header__btns button {
        font-size: 14px;
        padding: 13px 19px;
        min-width: 206px;
    }

}

@media (max-width: 1024px) {
    .header {
        border-radius: 0 0 10px 10px;
        background: var(--background-green);
        padding: 0;
        margin-bottom: 10px;
        position: fixed;
        top: 0;
        z-index: 100;
    }
    main {
        margin-top: 70px;
    }

    .header__logo {
        max-width: 26px;
        min-width: 20px;
    }

    .header__mobile {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        flex-grow: 1;
    }

    .header__phone {
        gap: 8px;
        font-size: 18px;
        flex-grow: 1;
        justify-content: center;
        &-icon {
            width: 32px;
            height: 32px;
            background: var(--white);
        }
    }

    .header__top, .header__bottom, .header__midle-text, .header__btns  {
        display: none;
    }

    .header__midle {
        gap: 20px;
    }

    .header__menu {
        display: none;
        & .header__phone {
            display: none;
        }
        &-mobile {
            display: flex;
            flex-direction: column;
            gap: 24px;
        }
    }
    .header__menu--active {
        position: fixed;
        top: 60px;
        left: 0;
        right: 0;
        width: 100%;
        z-index: 10;
        height: calc(100% - 60px);
        overflow-y: auto;
        padding: 20px 10px;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: flex-start;
        gap: 24px;
        &::-webkit-scrollbar {
            width: 3px;
        }
        &::-webkit-scrollbar-track {
            background: #DCDCDD;
            border-radius: 3px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 3px;
            background: var(--main-color);
        }

        & .header__nav {
            display: block;
            opacity: 1 !important;
        }
        & .header__btns {
            display: flex;
            & button {
                flex: 1 0 auto;
            }
        }
        & .header__info {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            gap: 20px;
        }
        & .header__item {
            & p {
                font-weight: 500;
                font-size: 14px;
                line-height: 136%;
                color: var(--text-color);
                opacity: 0.7;
                margin-bottom: 8px;
            }
            & > a {
                font-weight: 600;
                font-size: 16px;
                line-height: 124%;
                color: var(--main-color);
            }
        }
        & .header__networks {
            display: flex;
            gap: 8px;
        }
        & .header__network {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 500px;
            width: 36px;
            height: 36px;
            background: var(--main-color);
        }
    }


    
    .header__nav-list {
        flex-direction: column;
        gap: 8px;
    }
    .header__nav-item {
        position: relative;
        border-radius: 12px;
        background: var(--background-green);
        transition: all 0.3s ease;
        flex-wrap: wrap;
        gap: 0;

        &.active {
            background: var(--green-200);
            padding-bottom: 10px;
        }
    }
    .header__nav-item a {
        display: block;
        padding: 16px;
        font-size: 14px;
        opacity: 1;
        width: calc(100% - 30px);
    }
    .header__nav-item span {
        display: block;
        width: 30px;
        text-align: center;
        transform: rotate(0deg);
        &.active {
            transform: rotate(180deg);
        }
    }
    .header__sublist .header__nav-item a {
        width: calc(100% - 30px);
    }

    .header__nav-item .header__sublist, .header__nav-item .header__subsublist {
        opacity: 0;
        max-height: 0;
        overflow: hidden;
        pointer-events: none;
        position: static;
        border-radius: 0;
        box-shadow: none;
        background: transparent;
        min-width: auto;
        width: 100%;
        transition: all 0.3s ease-in;
    }
    .header__nav-item .header__subsublist {
        padding-left: 10px;
    }
    .header__nav-item .header__sublist.open, .header__nav-item .header__subsublist.open {
        opacity: 1;
        pointer-events: all;
    }
    .header__sublist .header__nav-item .header__nav-arrow {
        transform: rotate(0deg);
    }
    .header__sublist .header__nav-item .header__nav-arrow.active {
        transform: rotate(180deg);
    }
    .header__sublist .header__nav-item {
        &:nth-child(n) > a {
            padding: 6px 16px 6px 24px;
            border-radius: 0;
            &:hover {
                background: transparent;
                color: var(--main-color);
            }
        }
    }
}
@media (max-width: 550px) {
    .header__btns {
        flex-direction: column;
    }
}