
// faq
.faq {
    &__row {
        display: flex;
        gap: 60px;
    }

    &__left {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__item {
        &-question {
            border: 1px solid var(--line-green);
            border-radius: 16px;
            padding: 26px 30px;
            color: var(--text-color);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            & svg path {
                transition: all 0.4s ease;
            }
            
            &.accordion--active {
                margin-bottom: 8px;
                & .faq__item-btn svg path:first-child {
                    opacity: 0;
                    transform: rotateX(45deg);
                }
            }
        }

        &-btn {
            flex-shrink: 0;
        }

        &-answer {
            border-radius: 16px;
            padding: 26px 30px;
            background: var(--background-green);
        }
    }
}

@media (max-width: 1200px) {
    .faq {
        &__row {
            flex-direction: column;
            gap: 40px;
        }

        &__left {
            display: contents;
        }

        &__form {
            order: 3;
        }
    }
}
@media (max-width: 767px) {
    .faq {
        &__row {
            gap: 24px;
        }

        &__item {
            &-question {
                border-radius: 12px;
                padding: 16px;
                &.accordion--active {
                    margin-bottom: 4px;
                }
            }
            &-answer {
                border-radius: 12px;
                padding: 16px;
            }
        }
    }
}