
// contacts
.contacts {
    &__row {
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }

    &__contant {
        border-radius: 30px;
        padding: 36px 40px 40px;
        background: var(--background-green);
        flex: 1 0 30%;
    }

    &__items {
        display: flex;
        flex-direction: column;
        gap: 30px;
        margin-bottom: 50px;
    }

    &__item {
        &-text {
            font-size: 16px;
            line-height: 130%;
            color: var(--text-color);
            opacity: 0.7;
            margin-bottom: 10px;
        }

        &-info {
            font-weight: 500;
            font-size: 20px;
            line-height: 140%;
            color: var(--text-color);
        }

        &-phone {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 120%;
            letter-spacing: -0.01em;
            color: var(--main-color);
            transition: all 0.2s ease-in;
            &:hover {
                color: var(--secondary-color);
            }
        }
    }

    &__maps {
        border-radius: 30px;
        width: 100%;
        height: 100%;
        min-height: 623px;
        position: relative;
        overflow: hidden;
        flex: 1 0 60%;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }
}

@media (max-width: 1100px) {
    .contacts {
        &__row {
            flex-direction: column;
        }
    }
}
@media (max-width: 767px) {
    .contacts {
        &__row {
            gap: 10px;
        }
    
        &__contant {
            border-radius: 16px;
            padding: 20px 16px 16px;
        }
    
        &__items {
            gap: 24px;
            margin-bottom: 24px;
        }
    
        &__item {
            &-text {
                font-size: 14px;
            }
    
            &-info {
                font-size: 16px;
            }
    
            &-phone {
                font-size: 24px;
            }

            &-btn {
                width: 100%;
            }
        }
    
        &__maps {
            border-radius: 16px;
            min-height: 450px;
        }
    }
}