
// articles
.articles {
    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__swiper-slide {
        height: auto;
    }

    &__card {
        padding: 10px 10px 30px;
        border-radius: 30px;
        background: var(--background-green);
        display: flex;
        flex-direction: column;
        gap: 26px;
        height: 100%;
        transition: all 0.2s ease-in;
        &:hover {
            background: var(--green-100);
            & .articles__card-btn {
                color: var(--white);
                background: var(--main-color);
            }
        }

        &-top {
            border-radius: 28px;
            background: var(--white);
            position: relative;
        }

        &-img {
            & img {
                border-radius: 16px;
                width: 100%;
                height: 240px;
                object-position: top;
                margin: 0 auto;
            }
        }

        &-date {
            border-radius: 10px;
            padding: 16px 20px;
            background: var(--background-green);
            font-size: 16px;
            line-height: 120%;
            color: rgba(45, 49, 48, 0.6);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 12px;
            position: absolute;
            left: 10px;
            right: 10px;
            bottom: 10px;
        }

        &-main {
            padding: 0 20px;
            flex-grow: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 30px;
            color: rgba(45, 49, 48, 0.7);
        }

        &-text {
            font-size: 18px;
        }

        &-name {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 130%;
            letter-spacing: -0.01em;
            color: var(--main-color);
            margin-bottom: 16px;
        }

        &-btn {
            background: var(--green-100);
        }
    }
}

@media (max-width: 1440px) {
    .articles {
        &__cards {
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
        }
    }
}

@media (max-width: 1260px) {
    .articles {
        overflow: hidden;

        &__swiper {
            overflow: visible;
        }
        
        &__card {
            &-name {
                font-size: 22px;
            }
        }
    }
}
@media (max-width: 1100px) {
    .articles {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 14px;
        }
    }
}
@media (max-width: 767px) {
    .articles {
        &__card {
            padding: 8px 8px 16px;
            border-radius: 16px;
            gap: 16px;

            &-top {
                border-radius: 16px;
            }

            &-img {
                & img {
                    border-radius: 16px;
                    height: 180px;
                }
            }

            &-date {
                border-radius: 10px;
                padding: 12px 14px;
                font-size: 14px;
                left: 8px;
                right: 8px;
                bottom: 8px;
            }

            &-main {
                padding: 0 8px;
                gap: 24px;
            }

            &-text {
                font-size: 14px;
            }

            &-name {
                font-size: 18px;
                margin-bottom: 12px;
            }
        }
    }
}
@media (max-width: 600px) {
    .articles {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 8px;
        }
    }
}