
// error
.error {
    &-page {
        padding: 120px 0;
        margin: 0 auto;
        text-align: center;
    }
    &__number {
        font-weight: 700;
        font-size: 400px;
        line-height: 80%;
        color: var(--main-color);
        margin-bottom: 50px;
    }

    &__text {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 26px;
        line-height: 130%;
        letter-spacing: -0.01em;
        text-align: center;
        color: var(--text-color);
        opacity: 0.6;
        margin-bottom: 80px;
    }

    &__btn {
        display: block;
        width: fit-content;
        margin: 0 auto;
    }

    &__section {
        padding: 70px 20px;
    }

    &__links {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        & a {
            background: var(--white);
        }
    }
}
section.error {
    padding: 120px 0;
    margin: 0 auto;
    text-align: center;
}

@media (max-width: 1024px) {
    .error {
        &-page {
            padding: 100px 0;
        }
        &__number {
            font-size: 200px;
        }
        &__text {
            margin-bottom: 30px;
        }
    }
    section.error {
        padding: 100px 0;
    }
}
@media (max-width: 767px) {
    .error {
        &-page {
            padding: 60px 0;
        }
        &__number {
            font-size: 140px;
            margin-bottom: 20px;
        }
    
        &__text {
            font-size: 14px;
            margin-bottom: 40px;
        }
    
        &__btn {
            width: 100%;
        }
    
        &__section {
            padding: 20px 16px;
        }
    }
    section.error {
        padding: 60px 0;
    }
}