
// linking
.linking {
    &__col {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
        margin-bottom: 24px;

        &-item {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            border: 1px solid var(--line-green);
            border-radius: 16px;
            padding: 15px 15px 15px 24px;
            font-weight: 500;
            font-size: 20px;
            line-height: 140%;
            color: var(--text-color);
            height: 100%;
            transition: all 0.3s ease;

            &:hover {
                border: 1px solid var(--secondary-color);
                background: var(--secondary-color);
                color: var(--white);
            }

            &::after {
                content: '24';
                display: flex;
                align-items: center;
                justify-content: center;
                width: 60px;
                height: 100%;
                flex-shrink: 0;
                border-radius: 6px;
                padding: 14px 13px;
                background: var(--background-grey);
                color: var(--secondary-color);
            }
        }
    }

    &__price {
        padding: 22px 30px;
        border: 1px solid var(--line-green);
        border-radius: 16px;
        min-height: 200px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        color: var(--text-color);
        transition: all 0.3s ease;

        &:hover {
            border: 1px solid var(--secondary-color);
        }

        &-cost {
            font-weight: 500;
            font-size: 24px;
            line-height: 130%;
            color: var(--secondary-color);
        }
    }
}

@media (max-width: 1400px) {
    .linking {
        &__col {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
            margin-bottom: 24px;
        }
    }
}
@media (max-width: 1024px) {
    .linking {
        &__col {
            grid-template-columns: repeat(2, 1fr);
            gap: 16px;
        }
    }
}
@media (max-width: 767px) {
    .linking {
        &__col {
            gap: 10px;
            margin-bottom: 16px;

            &-item {
                border-radius: 8px;
                padding: 8px 8px 8px 16px;
                font-size: 14px;
                &::after {
                    width: 40px;
                    padding: 10px 11px;
                }
            }
        }

        &__price {
            padding: 16px;
            border-radius: 10px;
            min-height: 118px;
            &-cost {
                font-size: 16px;
            }
        }
    }
}
@media (max-width: 600px) {
    .linking {
        &__col {
            grid-template-columns: repeat(1, 1fr);
            gap: 8px;
        }
    }
}


.pansionats {
    &__swiper {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;

        & .swiper-wrapper {
            display: contents;
        }
    }
    &__swiper-slide {
        display: contents;
    }

    &-btn {
        margin: 40px auto 0;
        min-width: 300px;
    }
}

@media (max-width: 700px) {
    .pansionats {
        &__swiper {
            display: block;
            overflow: visible;
            & .swiper-wrapper {
                display: flex;
            }
        }
        &__swiper-slide {
            display: block;
            height: auto;
        }
        &-btn {
            display: none !important;
        }
    }
}