
// gallery
.gallery {
    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__card {
        display: block;
        border-radius: 30px;
        overflow: hidden;
        &-img img {
            max-height: 360px;
            min-height: 300px;
        }
    }

    &__btn {
        max-width: 300px;
        margin: 0 auto;
        margin-top: 40px;
    }
}

@media (max-width: 1140px) {
    .gallery {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 767px) {
    .gallery {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
    
        &__card {
            border-radius: 16px;
            &-img img {
                max-height: 280px;
                min-height: 220px;
            }
        }

        &__btn {
            max-width: 100%;
            margin-top: 24px;
        }
    }
}