
// popup-quiz 
.popup-quiz {
    &__content {
        padding: 60px;
        border-radius: 32px 40px;
        background: var(--main-color);
        color: var(--white);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 50px;
    }

    &__left {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        flex-basis: 60%;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 26px;
        line-height: 130%;
        letter-spacing: -0.01em;
        color: var(--white);
        margin-bottom: 30px;
    }

    &__text {
        opacity: 0.7;
        margin-bottom: 30px;
    }

    &__prices {
        margin-top: 30px;
        padding-bottom: 37px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        margin-bottom: 32px;
        display: flex;
        gap: 20px;
    }

    &__price {
        border-radius: 12px;
        padding: 14px 20px;
        background: var(--white);
        color: var(--secondary-color);
        & p {
            font-weight: 600;
            font-size: 20px;
            line-height: 120%;
            margin-bottom: 6px;
        }
        & span {
            font-weight: 500;
            font-size: 18px;
            line-height: 120%;
            text-decoration: line-through;
            color: var(--secondary-color);
            opacity: 0.7;
        }
    }

    &__radios {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__checkboxs {
        display: flex;
        flex-wrap: wrap;
        gap: 12px 50px;
        & > * {
            flex: 1 0 40%;
        }
    }
    
    &__bottom {
        display: flex;
        justify-content: space-between;
        gap: 12px;
    }
    &__btns {
        display: flex;
        gap: 12px;
    }
    &__btn-prev {
        background: rgba(255, 255, 255, 0.2);
    }
    &__btn-next:last-child {
        border: 1px solid var(--white);
    }

    &__paginations {
        border: 1px solid rgba(255, 255, 255, 0.2);
        border-radius: 15px 22px;
        padding: 10px;
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 24px;
        line-height: 120%;
        letter-spacing: -0.01em;
        color: var(--white);
        & span:last-child {
            color: rgba(255, 255, 255, 0.5);
        }
    }
}


// media popup-quiz
@media (max-width: 767px) {
    .popup-quiz {
        &__content {
            border-radius: 20px;
            padding: 30px 16px;
            flex-direction: column;
            gap: 46px;
        }
        &__title {
            font-size: 18px;
            margin-bottom: 16px;
        }



        &__text {
            margin-bottom: 20px;
        }
    
        &__prices {
            margin-top: 20px;
            margin-bottom: 29px;
            align-items: flex-start;
            flex-direction: column;
            gap: 8px;
        }
    
        &__price {
            border-radius: 10px;
            padding: 12px 10px;
            display: flex;
            gap: 8px;
            & p {
                font-size: 12px;
                margin-bottom: 0;
            }
            & span {
                font-size: 11px;
            }
        }
    
        &__radios {
            gap: 8px;
        }
    
        &__checkboxs {
            flex-direction: column;
            gap: 8px;
        }
        
        &__bottom {
            flex-direction: column-reverse;
        }
        &__btns {
            flex-direction: column-reverse;
            gap: 8px;
        }
    
        &__paginations {
            border-radius: 8px;
            padding: 10px;
            font-size: 18px;
            width: fit-content;
            margin-left: auto;
        }
    }
}
