// text-block
.text-block {
    &__row {
        display: flex;
        gap: 70px;
    }
    &__right {
        max-width: 440px;
        min-width: 440px;
    }

    & h2, & h3, & h4, & h5, & h6 {
        font-family: var(--second-family);
        font-weight: 600;
        line-height: 120%;
        letter-spacing: -0.01em;
        color: var(--text-color);
        margin-bottom: 20px;
        & span {
            color: var(--secondary-color);
        }
    }
    & h2 {
        font-size: 46px;
        margin-bottom: 30px;
    }
    & h3 {
        font-size: 32px;
    }
    & h4 {
        font-size: 26px;
    }
    & h5 {
        font-size: 22px;
    }
    & h2:not(:first-child), & h3:not(:first-child), & h4:not(:first-child), & h5:not(:first-child) {
        margin-top: 40px;
    }
    & h2:empty, & h3:empty, & h4:empty, & h5:empty {
        margin-bottom: 0;
    }

    & > p, & .container > p {
        line-height: 140%;
        color: rgba(45, 49, 48, 0.8);
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    & span {
        color: var(--main-color);
    }
    & a {
        color: var(--main-color);
        text-decoration: underline;
        transition: all 0.3s ease;
        &:hover {
            color: var(--secondary-color);
        }
    }

    & ul {
        display: flex;
        flex-direction: column;
        gap: 12px;
        margin-bottom: 20px;
        & li {
            padding-left: 26px;
            position: relative;
            &::before {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                border-radius: 100%;
                background-color: var(--main-color);
                position: absolute;
                top: 10px;
                left: 0;
            }
        }
    }

    & ol {
        display: flex;
        flex-direction: column;
        gap: 12px;
        margin-bottom: 20px;
        list-style-type: none;
        counter-reset: item;
        & li {
            padding-left: 46px;
            position: relative;
            &::before {
                content: counter(item, decimal-leading-zero) "."; 
                counter-increment: item;
                font-weight: 500;
                font-size: 20px;
                line-height: 130%;
                color: var(--main-color);
                position: absolute;
                top: 0px;
                left: 0;
            }
        }
    }

    & picture {
        float: right;
        max-width: 50%;
        & img {
            border-radius: 30px;
            width: 100%;
            max-height: 450px;
            object-fit: cover;
            margin: 10px;
        }
    }

    & .text-block__img {
        float: none; 
        width: 100%;
        & img {
            width: 100%;
            margin: 0;
            margin-bottom: 20px;
        }
    }

    & table {
        display: block;
        width: 100%;
        border: 1px solid var(--line-green);
        border-radius: 30px;
        overflow: hidden;
        border-collapse: collapse; 
        margin-bottom: 20px;

        & thead {
            & th {
                border: none;
                padding: 26px 30px;
                background: var(--color-svetlo-zelenyy-1);
                font-weight: 500;
                color: var(--text-color);
                text-align: left;
                border-right: 1px solid var(--line-green);
                
            }
            & tr th:last-child {
                border-right: none;
            }
        }
        
        & tbody {
            & td {
                padding: 26px 30px;
                font-size: 16px;
                color: var(--tekst-svetlyy);
                vertical-align: top;
                border-right: 1px solid var(--line-green);
            }
            & tr td:last-child {
                border-right: none;
            }
            & tr:nth-child(odd){
                background: var(--background-green);
            }
        }
    }

    & blockquote {
        border-radius: 20px;
        padding: 26px 32px 32px;
        background: var(--background-green);
        color: var(--text-color);
        position: relative;
        margin: 60px 0;

        &::after {
            content: "";
            display: block;
            width: 30px;
            height: 27px;
            background: url("./../img/icons/quote.svg") no-repeat center / contain;
            position: absolute;
            right: 32px;
            bottom: 32px;
        }
    }

    & .blockquote__author {
        margin-top: 26px;
        display: flex;
        align-items: center;
        gap: 12px;

        &-img {
            flex-shrink: 0;
            border-radius: 100px;
            width: 60px;
            height: 60px;
            background: var(--white);
            & img {
                margin: 0 !important;
            }
        }

        &-name {
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: var(--main-color);
            margin-bottom: 6px;
        }

        &-post {
            font-size: 14px;
            line-height: 130%;
            opacity: 0.6;
        }
    }

    & .block-orange {
        border-radius: 16px;
        padding: 26px 32px 32px;
        background: var(--secondary-color);
        color: var(--white);
        margin: 60px 0;
    }
}


// media text-block
@media (max-width: 1200px) {
    .text-block {
        &__right {
            display: none;
        }
        & h2 {
            font-size: 32px;
        }
        & h3 {
            font-size: 24px;
        }
        & h4 {
            font-size: 20px;
        }
        & h5 {
            font-size: 18px;
        }
    }
}
@media (max-width: 767px) {
    .text-block {
        & h2, & h3, & h4, & h5, & h6 {
            margin-bottom: 12px;
        }
        & h2 {
            font-size: 20px;
        }
        & h3 {
            font-size: 18px;
        }
        & h4 {
            font-size: 16px;
        }
        & h5 {
            font-size: 14px;
        }
        & h2:not(:first-child), & h3:not(:first-child), & h4:not(:first-child), & h5:not(:first-child) {
            margin-top: 20px;
        }

        & > p, & .container > p {
            &:not(:last-child) {
                margin-bottom: 12px;
            }
        }

        & ul {
            gap: 8px;
            margin-bottom: 12px;
            & li {
                padding-left: 18px;
                &::before {
                    min-width: 8px;
                    width: 8px;
                    height: 8px;
                    top: 6px;
                }
            }
        }
        & ol {
            gap: 8px;
            margin-bottom: 12px;
            & li {
                padding-left: 30px;
                &::before { 
                    font-size: 14px;
                }
            }
        }
        & picture {
            float: none;
            max-width: 100%;
            & img {
                max-height: 260px;
                margin: 20px 0;
            }
        }
        & table {
            overflow-x: auto;
            max-width: 100%;
            border-radius: 20px;
            & thead th, & tbody td {
                min-width: 243px;
                padding: 16px 20px;
            }
            & thead {
                & th {
                    font-size: 16px;
                }
            }
            & tbody {
                & td {
                    font-size: 14px;
                }
            }
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }

        & blockquote {
            border-radius: 16px;
            padding: 20px 16px;
            margin: 40px 0;
            &::after {
                content: none;
            }
        }
    
        & .blockquote__author {
            margin-top: 22px;
            gap: 10px;
            &-name {
                font-size: 14px;
                margin-bottom: 6px;
            }
            &-post {
                font-size: 12px;
            }
        }
    
        & .block-orange {
            border-radius: 12px;
            padding: 16px;
            margin: 40px 0;
        }
    }
}


.authors {
    &__cards {
        display: flex;
        gap: 20px;
    }
    &__card {
        flex: 1 0 40%;
        border-radius: 20px;
        padding: 20px 10px 10px;
        background: var(--background-green);

        &-text {
            font-size: 18px;
            margin-bottom: 18px;
            opacity: 0.6;
            padding: 0 14px;
        }

        &-wrap {
            padding: 0 14px;
            display: flex;
            align-items: center;
            gap: 12px;
            margin-bottom: 30px;
        }
    
        &-img {
            flex-shrink: 0;
            border-radius: 100px;
            width: 100px;
            height: 100px;
            background: var(--white);
            overflow: hidden;
            & img {
                margin: 0 !important;
            }
        }
    
        &-name {
            font-weight: 500;
            font-size: 22px;
            line-height: 130%;
            color: var(--main-color);
            margin-bottom: 6px;
        }
    
        &-post {
            font-size: 18px;
            line-height: 130%;
            opacity: 0.6;
        }

        &-date {
            border-radius: 10px;
            padding: 16px 20px;
            background: var(--white);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;
            font-size: 18px;
            opacity: 0.6;
        }
    }
}

@media (max-width: 1024px) {
    .authors {
        &__card {
            &-text {
                font-size: 16px;
            }

            &-img {
                width: 60px;
                height: 60px;
            }
        
            &-name {
                font-size: 16px;
            }
        
            &-post {
                font-size: 14px;
            }
    
            &-date {
                font-size: 16px;
            }
        }
    }
}
@media (max-width: 767px) {
    .authors {
        &__cards {
            gap: 10px;
        }
        &__card {
            border-radius: 12px;
            padding: 14px 16px;
    
            &-text {
                font-size: 12px;
                margin-bottom: 10px;
                padding: 0;
            }
    
            &-wrap {
                gap: 10px;
                margin-bottom: 16px;
                padding: 0;
            }
        
            &-name {
                font-size: 14px;
                margin-bottom: 6px;
            }
        
            &-post {
                font-size: 12px;
            }
    
            &-date {
                border-radius: 10px;
                padding: 12px 14px;
                font-size: 12px;
            }
        }
    }
}
@media (max-width: 680px) {
    .authors {
        &__cards {
            flex-direction: column;
            gap: 8px;
        }
    }
}

.pansionats-page {
    overflow: hidden;
    &__swiper {
        overflow: visible;
    }
}


// navigation
.navigation {
    &-wrap {
        border-radius: 10px;
        background: var(--background-green);
        max-width: 850px;
        border: 1px solid transparent;
        transition: all 0.2s ease-in;

        &:hover {
            border: 1px solid rgba(80, 174, 163, 0.5);
        }
    }

    &-acc {
        padding: 21px 26px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 12px;
        color: var(--text-color);
    }

    &-more {
        display: block;
        transition: all 0.2s ease-in;
    }

    &-inner {
        padding: 26px;
        padding-top: 0;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 16px;
        list-style-type: none;
        counter-reset: item;

        & li {
            display: flex;
            gap: 10px;
            opacity: 0.7;
            transition: all 0.2s ease-in;
            &::before {
                content: counter(item) "."; 
                counter-increment: item;
                font-weight: 500;
                font-size: 16px;
                line-height: 130%;
                color: var(--main-color);
            }
            &:hover {
                opacity: 1;
            }
        }

        & a {
            display: block;
            font-size: 16px;
            line-height: 124%;
            color: var(--text-color);
            width: 100%;
        }
    }
}

@media (max-width: 767px) {
    .navigation {
        &-wrap {
            border-radius: 12px;
        }
    
        &-acc {
            padding: 16px;
        }
    
        &-inner {
            padding: 16px;
            padding-top: 0;
        }
    
        &__list {
            gap: 12px;
            & li {
                gap: 8px;
                &::before {
                    font-size: 14px;
                }
            }
    
            & a {
                font-size: 14px;
            }
        }
    }
}