// calculator
.calculator {
    padding: 40px;

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 60px;
    }

    &__left {
        flex: 1 0 40%;
        padding: 40px;
    }

    &__title {
        margin-bottom: 69px;
    }

    &__questions {
        display: flex;
        flex-wrap: wrap;
        gap: 50px 40px;
    }

    &__question {
        flex: 1 0 40%;
        &-text {
            font-weight: 600;
            font-size: 20px;
            line-height: 120%;
            color: var(--text-color);
            margin-bottom: 16px;
        }

        &-items {
            display: flex;
            flex-direction: column;
            gap: 12px;
        }
    }

    &__right {
        flex: 1 0 45%;
        padding: 40px;
        border-radius: 38px;
        background: var(--main-color);
    }

    &__result {
        padding: 22px 24px;
        border-radius: 20px;
        background: var(--white);
        font-weight: 500;
        font-size: 16px;
        line-height: 130%;
        display: flex;
        gap: 30px;
        margin-bottom: 60px;
        &-res {
            flex: 1 0 40%;
            padding-right: 30px;
            border-right: 1px solid var(--line-green);
            & p {
                opacity: 0.7;
                margin-bottom: 10px;
            }
            & span {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 26px;
                line-height: 130%;
                letter-spacing: -0.01em;
                color: var(--main-color);
            }
        }

        &-text {
            flex: 1 0 40%;
            color: var(--text-green);
            opacity: 0.7;
        }
    }

    &__form {
        &-row {
            display: flex;
            flex-wrap: wrap;
            gap: 12px;
            & > * {
                flex: 1 0 40%;
            }
            & > textarea {
                flex: 1 0 100%;
            }
        }
    }
}

@media (max-width: 1500px) {
    .calculator {
        padding: 20px;

        &__left {
            padding: 20px;
        }
    }
}
@media (max-width: 1360px) {
    .calculator {
        & .container {
            padding: 0;
        }
        &__row {
            flex-direction: column;
            gap: 30px;
        }
        &__title {
            margin-bottom: 40px;
        }
    }
}
@media (max-width: 767px) {
    .calculator {
        padding: 24px 10px;

        &__left {
            padding: 0 6px;
        }
    
        &__title {
            margin-bottom: 24px;
        }
    
        &__question {
            &-text {
                font-size: 16px;
                margin-bottom: 10px;
            }
    
            &-items {
                gap: 10px;
            }
        }
    
        &__right {
            border-radius: 16px;
            padding: 8px 8px 20px 8px;
        }
    
        &__result {
            border-radius: 12px;
            padding: 16px;
            font-size: 12px;
            flex-direction: column;
            gap: 16px;
            margin-bottom: 20px;
            &-res {
                padding-right: 0;
                padding-bottom: 20px;
                border-right: none;
                border-bottom: 1px solid var(--line-green);
                & span {
                    font-size: 20px;
                }
            }
        }
    
        &__form {
            &-row {
                gap: 12px;
                & > * {
                    flex: 1 0 100%;
                }
            }
        }
    }
}
@media (max-width: 580px) {
    .calculator {
        &__questions {
            flex-direction: column;
            gap: 24px;
        }
    }
}