// footer
.footer {
    background: var(--text-color);
    padding: 80px 40px;
    margin-bottom: 20px;
    color: rgba(255, 255, 255, 0.7);

    &__row {
        display: flex;
        gap: 60px;
    }

    &__contant {
        display: flex;
        flex-direction: column;
        gap: 40px;
        min-width: 432px;

        &-text {
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
        }
    }

    &__logo {
        display: block;
        max-width: 205px;
        margin-bottom: 24px;
    }

    &__items {
        display: flex;
        flex-direction: column;
        gap: 40px;
    }

    &__networks {
        display: flex;
        gap: 10px;
    }

    &__network {
        border-radius: 500px;
        width: 40px;
        height: 40px;
        background: var(--main-color);
        border: 1px solid var(--main-color);
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.3s ease;
        & svg path {
            transition: all 0.3s ease;
        }
        &:hover {
            background: transparent;
            & svg path {
                fill: var(--main-color);
            }
        }
    }

    &__item {
        font-weight: 500;
        font-size: 16px;
        line-height: 130%;
        display: flex;
        flex-direction: column;
        gap: 12px;
    }
    
    &__link {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 32px;
        line-height: 120%;
        letter-spacing: -0.01em;
        color: var(--main-color);
        &:hover {
            text-decoration: underline;
        }
    }

    &__btn {
        max-width: 50%;
    }


    &__right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
    }

    &__nav {
        display: flex;
        justify-content: space-between;
        gap: 30px;

        & a {
            display: block;
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: rgba(255, 255, 255, 0.7);
            padding: 5px 0;
            transition: all 0.3s ease;
            &:hover {
                opacity: 0.6;
            }
        }

        &-acc, & a.footer__nav-acc {
            display: block;
            width: fit-content;
            font-weight: 500;
            font-size: 18px;
            color: var(--white);
            margin-bottom: 20px;
        }
    }

    &__bottom {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        padding-top: 30px;
        border-top: 1px solid #c7d6dc;

        &_links {
            display: flex;
            flex-direction: column;
            gap: 16px;
            & a {
                display: block;
                font-weight: 500;
                font-size: 16px;
                line-height: 130%;
                color: rgba(255, 255, 255, 0.7);
                transition: all 0.3s ease;
                &:hover {
                    opacity: 0.6;
                }
            }
        }
    }

    &__copy {
        max-width: 34%;
        font-size: 14px;
        & p:not(:last-child) {
            margin-bottom: 20px;
        }
    }
}

@media (max-width: 1500px) {
    .footer {
        &__nav {
            flex-wrap: wrap;
            gap: 30px;
    
            &-item {
                width: calc(50% - 15px);
            }
        }
    }
}
@media (max-width: 1200px) {
    .footer {
        &__row {
            flex-direction: column;
            gap: 40px;
        }
        &__contant {
            min-width: auto;
        }
    }
}
@media (max-width: 767px) {
    .footer {
        padding: 24px 16px;
        margin-bottom: 10px;
    
        &__row {
            gap: 30px;
        }
    
        &__contant {
            gap: 30px;
            &-text {
                font-size: 14px;
            }
        }
    
        &__logo {
            max-width: 179px;
            margin-bottom: 16px;
        }
    
        &__items {
            gap: 24px;
        }
        &__network {
            width: 36px;
            height: 36px;
        }
    
        &__item {
            font-size: 14px;
            gap: 8px;
        }
        
        &__link {
            font-size: 24px;
        }
    
        &__btn {
            max-width: 100%;
        }
    
    
        &__right {
            gap: 30px;
        }
    
        &__nav {
            gap: 24px;
            & a {
                font-size: 12px;
                padding: 4px 0;
            }
            &-acc, & a.footer__nav-acc {
                font-size: 14px;
                margin-bottom: 12px;
            }
        }
    
        &__bottom {
            flex-direction: column;
            align-items: stretch;
            gap: 24px;
    
            &_links {
                gap: 8px;
                & a {
                    font-size: 12px;
                }
            }
        }
    
        &__copy {
            max-width: 100%;
            font-size: 12px;
            & p:not(:last-child) {
                margin-bottom: 16px;
            }
        }
    }
}

@media (max-width: 560px) {
    .footer {
        padding: 24px 6px;
        &__nav {
            flex-direction: column;
            &-item {
                width: 100%;
            }
        }
    }
}