
// license
.license {
    overflow: hidden;
    
    &__swiper {
        overflow: visible;
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__card {
        display: block;
        border-radius: 30px;
        overflow: hidden;
        position: relative;
        &-img img {
            max-height: 600px;
            min-height: 400px;
            object-position: top;
        }

        &-name {
            border-radius: 16px;
            padding: 20px 24px;
            background: var(--white);
            font-weight: 500;
            font-size: 18px;
            line-height: 130%;
            color: rgb(45,49,4, 0.7);
            position: absolute;
            bottom: 20px;
            left: 20px;
            right: 20px;
        }
    }
}

@media (max-width: 1300px) {
    .license {
        &__cards {
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 1024px) {
    .license {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 660px) {
    .license {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
    
        &__card {
            border-radius: 16px;
            &-img img {
                max-height: 460px;
                min-height: 400px;
            }
            &-name {
                border-radius: 10px;
                padding: 14px 16px;
                font-size: 14px;
                bottom: 8px;
                left: 8px;
                right: 8px;
            }
        }
    }
}