// included
.included {
    &-main {
        padding: 70px 20px 80px;
        & .included__card {
            background: var(--white);
            &--green {
                background: var(--background-green);
            }
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
        margin-bottom: 50px;
    }

    &__swiper {
        overflow: visible;
    }

    &__swiper-slide {
        height: auto;
    }

    &__card {
        padding: 25px 32px 32px;
        border-radius: 30px;
        background: var(--background-green);
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 90px;
        height: 100%;

        &::before {
            content: "";
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 100%;
            background: var(--secondary-color);
            opacity: 0.5;
            position: absolute;
            top: 32px;
            right: 32px;
        }

        &-text {
            font-size: 18px;
        }

        &-name {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 130%;
            letter-spacing: -0.01em;
            color: var(--main-color);
            margin-bottom: 14px;
            max-width: 86%;
        }

        &-img {
            max-height: 200px;
            width: auto;
            & img {
                width: auto;
            }
        }
    }

    &__swiper-pagination {
        display: block;
    }
}

@media (max-width: 850px) {
    .included {
        &-main {
            padding: 0;
            background: transparent;
            & .included__card {
                background: var(--background-green);
            }
        }

        &__top {
            margin-bottom: 24px;
        }
        &__card {
            padding: 16px 16px 20px;
            border-radius: 16px;
            gap: 60px;
    
            &::before {
                width: 8px;
                height: 8px;
                top: 20px;
                right: 20px;
            }
    
            &-text {
                font-size: 14px;
            }
    
            &-name {
                font-size: 18px;
                margin-bottom: 12px;
                max-width: 86%;
            }
    
            &-img {
                max-height: 150px;
            }
        }
    }    
}