// banner
.banner {
    &__title {
        margin-bottom: 40px;
    }

    &__row {
        display: flex;
        gap: 60px;
    }

    &__info {
        &-location {
            border-radius: 12px;
            padding: 20px;
            background: var(--background-green);
            font-weight: 500;
            font-size: 16px;
            line-height: 120%;
            color: rgba(45, 49, 48, 0.7);
            display: flex;
            gap: 12px;
            margin-bottom: 30px;
            &::before {
                content: "";
                display: block;
                width: 14px;
                height: 16px;
                background: url("./../img/icons/location.svg") no-repeat center / contain;
                flex-shrink: 0;
            }

            & a {
                display: block;
                font-weight: 500;
                line-height: 130%;
                text-decoration: underline;
                text-decoration-skip-ink: none;
                color: var(--main-color);
                transition: all 0.2s ease-in;
                &:hover {
                    opacity: 0.7;
                }
            }
        }

        &-ratings {
            display: flex;
            gap: 20px;
            margin-bottom: 30px;
        }

        &-rating {
            display: flex;
            align-items: center;
            gap: 8px;
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: rgb(45,49,48, 0.7);
            & span {
                font-weight: 600;
                font-size: 18px;
                line-height: 120%;
                color: var(--text-color);
            }
            &:last-child {
                & p {
                    display: none;
                }
            }
        }
        &-ratingStars {
            display: flex;
            gap: 4px;
        }

        &-prices {
            display: flex;
            gap: 16px;
            padding-bottom: 40px;
            border-bottom: 1px solid var(--line-green);
            margin-bottom: 40px;
        }

        &-price {
            font-weight: 600;
            line-height: 120%;
            color: var(--secondary-color);
            &:first-child {
                padding-right: 16px;
                border-right: 1px solid var(--line-orange);
            }
            & p {
                font-size: 20px;
                margin-bottom: 6px;
            }
            & span {
                font-size: 18px;
                opacity: 0.7;
                text-decoration: line-through;
            }
        }

        &-phone {
            margin-bottom: 30px;
            & p {
                font-size: 16px;
                line-height: 130%;
                color: var(--text-color);
                opacity: 0.7;
                margin-bottom: 10px;
            }
            & a {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 32px;
                line-height: 120%;
                letter-spacing: -0.01em;
                color: var(--main-color);
                &:hover {
                    text-decoration: underline;
                }
            }
        }

        &-items {
            display: flex;
            gap: 10px;
            flex-wrap: wrap;
            margin-bottom: 30px;
        }

        &-item {
            flex: 1 0 20%;
            border-radius: 10px;
            padding: 13px 16px;
            background: var(--background-green);
            font-weight: 500;
            font-size: 14px;
            line-height: 120%;
            color: rgba(45, 49, 48, 0.6);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 16px;
            & span {
                display: block;
                font-weight: 600;
                font-size: 16px;
                color: var(--text-color);
                margin-top: 4px;
            }
        }

        &-btns {
            display: flex;
            gap: 10px;
            & > * {
                flex-grow: 1;
            }
        }
    }

    &__right {
        max-width: 50%;
        display: flex;
        flex-direction: column;
        position: relative;

        &-tags {
            display: flex;
            flex-wrap: wrap;
            gap: 6px;
            position: absolute;
            top: 12px;
            left: 12px;
            right: 12px;
            z-index: 3;
        }

        &-tag {
            border-radius: 8px;
            padding: 10px 12px;
            background: var(--white);
            display: flex;
            align-items: center;
            gap: 10px;
            font-weight: 600;
            font-size: 14px;
            line-height: 130%;
            color: var(--text-color);
            &::before {
                content: "";
                display: block;
                width: 16px;
                height: 16px;
                background: url("./../img/icons/tags/tag-1.svg") no-repeat center / contain;
            }
            &[data-name="Самая доступная цена"]::before {
                background: url("./../img/icons/tags/tag-2.svg") no-repeat center / contain;
            }
            &[data-name="Выбор родственников"]::before {
                background: url("./../img/icons/tags/tag-3.svg") no-repeat center / contain;
            }
            &[data-name="Одобрено врачами"]::before {
                background: url("./../img/icons/tags/tag-4.svg") no-repeat center / contain;
            }
        }
    }

    &__swiper {
        flex-grow: 1;
        height: auto;
        margin-bottom: 16px; 
        border-radius: 24px;

        &-slide {
            max-height: 580px;
        }
        &-img {
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 24px;
            overflow: hidden;

            & picture {
                display: block;
                width: 100%;
                height: 100%;
            }
        }
        &-button-next, &-button-prev {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 50px;
            height: 50px;
        }
    }

    &__swiper2 {
        height: auto;
        &-slide {
            max-height: 110px;
            min-height: 110px;
        }
        &-img {
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 16px;
            overflow: hidden;
        }
    }
}

@media (max-width: 1500px) {
    .banner {
        &__info {
            &-item {
                flex: 1 0 40%;
            }
        }
    }
}
@media (max-width: 1120px) {
    .banner {
        &__row {
            flex-direction: column;
            gap: 0px;
        }
    
        &__info {
            display: contents;

            &-main {
                display: contents;
            }

            &-ratings {
                order: -2;
            }

            &-item {
                flex: 1 0 20%;
            }
        }
    
        &__right {
            max-width: 100%;
            order: -1;
            margin-bottom: 30px;
        }
    
        &__swiper {
            margin-bottom: 0; 
            &-slide {
                max-height: 500px;
            }
        }
    
        &__swiper2 {
            display: none;
        }
    }
}
@media (max-width: 767px) {
    .banner {
        &__title {
            margin-bottom: 16px;
        }

        &__info {
            &-location {
                padding: 12px;
                font-size: 12px;
                gap: 8px;
                // border: 1px solid var(--line-green);
                // background: transparent;
                margin-bottom: 16px;
            }
    
            &-ratings {
                flex-direction: column-reverse;
                gap: 6px;
                margin-bottom: 20px;
            }
    
            &-rating {
                font-size: 14px;
                & span {
                    font-size: 16px;
                }
                &:last-child {
                    flex-direction: row-reverse;
                    justify-content: start;
                    & p {
                        display: block;
                    }
                }
            }

            &-prices {
                flex-direction: column;
                gap: 10px;
                padding-bottom: 30px;
                margin-bottom: 24px;
            }
    
            &-price {
                display: flex;
                gap: 8px;
                &:first-child {
                    padding-right: 0;
                    border-right: none;
                }
                & p {
                    font-size: 16px;
                    margin-bottom: 0;
                }
                & span {
                    font-size: 14px;
                    text-decoration: line-through;
                }
            }
    
            &-phone {
                margin-bottom: 24px;
                & p {
                    font-size: 14px;
                    margin-bottom: 8px;
                }
                & a {
                    font-size: 24px; 
                }
            }
    
            &-item {
                flex: 1 0 40%;
                padding: 12px;
                font-size: 12px;
                gap: 16px;
                & span {
                    font-size: 14px;
                }
            }
    
            &-btns {
                flex-direction: column;
                gap: 8px;
            }
        }
    
        &__right {
            margin-bottom: 20px;
    
            &-tags {
                top: 6px;
                left: 6px;
                right: 6px;
            }
    
            &-tag {
                border-radius: 6px;
                padding: 8px 10px;
                font-size: 12px;
                &::before {
                    width: 12px;
                    height: 12px;
                }
            }
        }
    
        &__swiper {
            margin-bottom: 0; 
            border-radius: 12px;
    
            &-slide {
                max-height: 300px;
            }
            &-img {
                border-radius: 12px;
                & img {
                    max-height: 300px;
                    min-height: 180px;
                }
            }
        }
    }
}