
// check
.check {
    &__list {
        display: flex;
        justify-content: space-between;
        list-style-type: none;
        counter-reset: item;
    }

    &__item {
        font-size: 18px;
        color: rgba(45, 49, 48, 0.7);
        flex-grow: 1;
        padding-top: 30px;
        border-top: 2px solid var(--secondary-color);
        position: relative;
        padding-right: 40px;

        &-title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 130%;
            letter-spacing: -0.01em;
            color: var(--main-color);
            max-width: 195px;
            margin-bottom: 20px;
        }

        &-text {
            max-width: 351px;
            & a {
                font-size: 20px;
                line-height: 120%;
                color: var(--secondary-color);
                text-wrap: nowrap;
            }
        }

        &::before {
            content: counter(item, decimal-leading-zero); 
            counter-increment: item;
            display: block;
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 60px;
            line-height: 120%;
            letter-spacing: -0.01em;
            color: var(--main-color);
            opacity: 0.2;
            margin-bottom: 16px;
        }

        &::after {
            content: "";
            display: block;
            width: 10px;
            height: 10px;
            border-radius: 100%;
            background: var(--secondary-color);
            position: absolute;
            top: -5px;
            left: 0;
        }
    }
}

@media (max-width: 1200px) {
    .check {
        &__list {
            flex-wrap: wrap;
            gap: 40px 0;
        }
        &__item {
            flex: 1 0 40%;
            &-title {
                font-size: 22px;
            }
        }
    }
}
@media (max-width: 767px) {
    .check {
        &__list {
            flex-direction: column;
            gap: 0;
        }

        &__item {
            font-size: 14px;
            padding-top: 0;
            padding-right: 0;
            padding-left: 20px;
            border-top: none;
            border-left: 2px solid var(--secondary-color);
            &:not(:last-child) {
                padding-bottom: 30px;
            }

            &-title {
                font-size: 18px;
                max-width: none;
                margin-bottom: 6px;
            }

            &-text {
                max-width: 351px;
                & a {
                    font-size: 14px;
                }
            }

            &::before {
                font-size: 32px;
                margin-bottom: 12px;
            }

            &::after {
                width: 8px;
                height: 8px;
                top: 0;
                left: -5px;
            }
        }
    }
}