// intro
.intro {
    position: relative;
    overflow: hidden;

    &__swiper-slide {
        height: auto;
        border-radius: 30px;
    }

    &__swiper-btns {
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 3;
    }

    &__wrap {
        padding: 30px 20px 80px;
        max-width: 45%;
        height: 100%;
        
        &-main {
            padding: 67px 20px 80px;
        }
    }
    
    & h1 {
        margin-bottom: 30px;
    }
    
    &__inner {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: start;
        gap: 40px;
        min-height: 500px;
        position: relative;
    }

    &__img {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        border-radius: 40px;
        width: 50%;
        & img {
            object-position: top;
        }
    }
}


@media (max-width: 1150px) {
    .intro {
        &__swiper-btns {
            right: -54px;
        }
    }
}
@media (max-width: 1024px) {
    .intro {
        & .container {
            padding: 0;
        }

        &__swiper-btns {
            right: 0;
        }
    
        &__wrap {
            padding: 30px 30px 46px;
            max-width: 100%;
        }
        
        & h1 {
            margin-bottom: 16px;
        }
        
        &__inner {
            min-height: auto;
        }
    
        &__img {
            position: static;
            border-radius: 40px;
            width: 100%;
            max-height: 550px;
        }
    }
}
@media (max-width: 767px) {
    .intro {
        &__wrap {
            padding: 32px 16px 46px;
        }
        &__img {
            border-radius: 20px;
            max-height: 400px;
        }
        &__btn {
            width: 100%;
        }
    }
}