
// form
.form {
    border-radius: 24px;
    padding: 26px 30px 30px 30px;
    background: var(--main-color);
    min-width: 440px;

    &-title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 32px;
        line-height: 120%;
        letter-spacing: -0.01em;
        color: var(--white);
        margin-bottom: 16px;
    }

    &-subtitle {
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        color: var(--white);
        opacity: 0.8;
        margin-bottom: 50px;
    }

    &-inner {
        display: flex;
        flex-direction: column;
        gap: 20px;

        &__row {
            display: flex;
            gap: 10px;
        }
    }
}

@media (max-width: 767px) {
    .form {
        border-radius: 16px;
        padding: 24px 16px;
        min-width: auto;
    
        &-title {
            font-size: 24px;
            margin-bottom: 12px;
        }
    
        &-subtitle {
            font-size: 14px;
            margin-bottom: 40px;
        }
    
        &-inner {
            gap: 16px;
            
            &__row {
                flex-direction: column;
                gap: 16px;
            }
        }
    }   
}