
// price
.price {
    &__row {
        display: flex;
        justify-content: space-between;
        gap: 40px;
    }

    &__title {
        flex: 1 0 20%;
        &-label {
            border: 1px solid var(--line-green);
            border-radius: 12px;
            padding: 16px 20px;
            opacity: 0.8;
            width: fit-content;
        }
    }
    
    &__table {
        flex: 1 0 60%;
        & table {
            display: block;
            width: 100%;
            border: none;
            border-radius: 30px;
            overflow: hidden;
            border-collapse: collapse; 

            & tbody {
                display: block;
                width: 100%;
                & tr {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    gap: 20px;
                    padding: 30px;
                }
                & td {
                    color: var(--text-color);
                    vertical-align: top;
                }
                & tr td:first-child {
                    flex: 1 0 70%;
                }
                & tr td:last-child {
                    font-weight: 600;
                    text-align: right;
                    color: var(--main-color);
                    flex: 1 0 20%;
                }
                & tr:nth-child(odd){
                    background: var(--background-green);
                }
            }
        }
    }
}

@media (max-width: 1024px) {
    .price {
        &__row {
            flex-direction: column;
            gap: 40px;
        }
    }
}
@media (max-width: 767px) {
    .price {
        &__row {
            gap: 24px;
        }

        &__title {
            &-label {
                border-radius: 8px;
                padding: 12px 16px;
                font-size: 14px;
            }
        }
        
        &__table {
            & table {
                border-radius: 16px;

                & tbody {
                    & tr {
                        padding: 12px 16px;
                    }
                    & tr td:first-child {
                        flex: 1 0 60%;
                    }
                    & tr td:last-child {
                        flex: 1 0 30%;
                    }
                }
            }
        }
    }
}
@media (max-width: 360px) {
    .price {
        &__table {
            & table {
                border-radius: 16px;

                & tbody {
                    & tr {
                        flex-direction: column;
                        align-items: stretch;
                        gap: 6px;
                    }
                    & tr td:last-child {
                        text-align: left;
                    }
                }
            }
        }
    }
}