@charset "UTF-8";
/* Reset and base styles  */
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */
a, a:link, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* Common */
aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */
input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button, input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

:root {
  --container-width: 1740px;
  --container-padding: 10px;
  --font-family: "Montserrat", sans-serif;
  --second-family: "Manrope", sans-serif;
  --main-color: #50aea3;
  --secondary-color: #6d8eeb;
  --text-color: #2d3130;
  --text-green: #34635b;
  --background-green: #f4f8f9;
  --white: #fff;
  --line: #d8e8e6;
  --green-200: rgba(80, 174, 163, 0.2);
  --main-color: #50aea3;
  --secondary-color: #ec7a4d;
  --text-color: #2d3130;
  --text-green: #34635b;
  --background-green: #f4f8f9;
  --background-grey: #f8f8f8;
  --white: #fff;
  --line-green: #d8e8e6;
  --line-orange: #efe8e6;
  --green-200: rgba(80, 174, 163, 0.2);
  --green-100: rgba(80, 174, 163, 0.1);
}

/* Montserrat */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-display: swap;
  font-weight: 600;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
/* Manrope */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/Manrope-Regular.woff2") format("woff2"), url("../fonts/Manrope-Regular.woff") format("woff");
}
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: url("../fonts/Manrope-Medium.woff2") format("woff2"), url("../fonts/Manrope-Medium.woff") format("woff");
}
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-display: swap;
  font-weight: 600;
  src: url("../fonts/Manrope-SemiBold.woff2") format("woff2"), url("../fonts/Manrope-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-display: swap;
  font-weight: 700;
  src: url("../fonts/Manrope-Bold.woff2") format("woff2"), url("../fonts/Manrope-Bold.woff") format("woff");
}
/* Общие glob */
html.lock {
  overflow: hidden;
}

html.burger-lock {
  overflow: hidden;
}

html.burger-lock::before, html.lock::before {
  opacity: 1;
  pointer-events: all;
}

html::before {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3215686275);
  position: fixed;
  inset: 0;
  opacity: 0;
  z-index: 4;
  pointer-events: none;
  -webkit-transition: opacity 0.3s linear;
  -o-transition: opacity 0.3s linear;
  transition: opacity 0.3s linear;
}

.no-scroll {
  overflow: hidden;
}

body.lock {
  overflow: hidden;
}

body.burger-lock {
  overflow: hidden;
}

body {
  margin: 0;
  overflow-y: auto;
  padding: 0;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  color: rgba(45, 49, 48, 0.8);
  background: var(--white);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-padding);
  max-width: var(--container-width);
  width: 100%;
}

@media (max-width: 1720px) {
  .container {
    max-width: 1600px;
  }
}
.page__top {
  margin-bottom: 50px;
}

.section-offset {
  margin-bottom: 160px;
  overflow: hidden;
}

.section-offset-mini {
  margin-bottom: 100px;
}

.section__title {
  margin-bottom: 40px;
}

.section__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 40px;
}

.green-section {
  margin-right: 20px;
  margin-left: 20px;
  background: var(--background-green);
  border-radius: 40px;
  position: relative;
  overflow: hidden;
}

/*  buttons */
.btn-green, .btn-orange, .btn-white {
  display: block;
  border-radius: 16px;
  padding: 23px 40px;
  font-weight: 600;
  font-size: 20px;
  line-height: 120%;
  text-align: center;
  transition: all 0.3s ease;
}
.btn-green:disabled, .btn-orange:disabled, .btn-white:disabled {
  opacity: 0.6;
  pointer-events: none;
}

.btn-small {
  border-radius: 12px;
  padding: 16px 23px;
  font-size: 18px;
}

.btn-green {
  background: var(--main-color);
  color: var(--white);
}
.btn-green:hover {
  background: var(--secondary-color);
}

.btn-orange {
  background: var(--secondary-color);
  color: var(--white);
}
.btn-orange:hover {
  background: var(--main-color);
}

.btn-white {
  background: var(--white);
  color: var(--main-color);
  border: 1px solid var(--white);
}
.btn-white:hover {
  color: var(--white);
  background: var(--main-color);
}

.btn-more, .btn-green-text {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  text-decoration: underline !important;
  color: var(--main-color);
  transition: all 0.3s ease;
}
.btn-more:hover, .btn-green-text:hover {
  color: var(--text-green);
}

.links {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.link {
  display: block;
  border-radius: 12px;
  padding: 16px 20px 16px 46px;
  background: #f2faf6;
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  color: var(--text-color);
  position: relative;
  transition: all 0.3s ease;
}
.link::after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background: var(--main-color);
  position: absolute;
  top: 25px;
  left: 20px;
  transition: all 0.3s ease;
}
.link:hover, .link.active {
  background: var(--main-color);
  color: var(--white);
}
.link:hover::after, .link.active::after {
  background: var(--white);
}

/*  titles  */
.title-h1, .title-h2, .title-h3 {
  font-family: var(--second-family);
  font-weight: 600;
  line-height: 120%;
  letter-spacing: -0.01em;
  color: var(--text-color);
}
.title-h1 span, .title-h2 span, .title-h3 span {
  color: var(--secondary-color);
}

.title-h1 {
  font-size: 54px;
}

.title-h2 {
  font-size: 46px;
}

.title-h3 {
  font-size: 32px;
}

.title-br span {
  display: block;
}

.img-cover {
  display: block;
  overflow: hidden;
}
.img-cover img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/*  form  */
.input, .textarea {
  border-radius: 16px;
  padding: 23px 24px;
  background: rgba(255, 255, 255, 0.2);
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;
  color: rgb(255, 255, 255);
  border: 1px solid transparent;
  width: 100%;
  transition: all 0.3s ease;
}
.input::placeholder, .textarea::placeholder {
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;
  color: rgba(255, 255, 255, 0.6);
}
.input:hover, .input:focus, .input:active, .textarea:hover, .textarea:focus, .textarea:active {
  border: 1px solid rgba(255, 255, 255, 0.5);
  background: rgba(255, 255, 255, 0.1);
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.textarea {
  min-height: 142px;
  resize: none;
}

.politic {
  display: flex;
  gap: 12px;
  align-items: center;
}
.politic input[type=checkbox] {
  position: relative;
  flex-shrink: 0;
  border: 1px solid var(--line-green);
  border-radius: 4px;
  width: 20px;
  height: 20px;
  color: #000;
  appearance: none;
  outline: 0;
  cursor: pointer;
  transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
}
.politic input[type=checkbox]::before {
  position: absolute;
  content: "";
  display: block;
  top: 3px;
  left: 7px;
  width: 5px;
  height: 9px;
  border-style: solid;
  border-color: var(--main-color);
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  opacity: 0;
}
.politic input[type=checkbox]:checked {
  color: #fff;
  border-color: var(--white);
  background: var(--white);
}
.politic input[type=checkbox]:checked::before {
  opacity: 1;
}
.politic label {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 130%;
  color: rgba(255, 255, 255, 0.7);
}
.politic label a {
  color: inherit;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  transition: all 0.3s ease;
}
.politic label a:hover {
  opacity: 0.5;
}
.politic label::before {
  position: absolute;
  content: attr(data-content);
}

/*  search  */
.form-search {
  position: relative;
  width: 100%;
}
.form-search input {
  background: var(--fon-plashek);
  opacity: 0.7;
  width: 100%;
}
.form-search input:focus {
  opacity: 1;
}
.form-search img {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translate(0, -50%);
}

/* select */
.select {
  cursor: pointer;
  outline: none;
  border-radius: 8px;
  padding: 12px 16px;
  background: var(--background-green);
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: var(--text-color);
  padding-right: 30px;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: var(--background-green) url(./../img/icons/arrow-bottom.svg) no-repeat right;
  background-position-x: calc(100% - 16px);
  transition: all 0.3s ease;
}

.sort-checkbox {
  display: flex;
  gap: 12px;
  align-items: center;
}
.sort-checkbox input[type=checkbox] {
  position: relative;
  flex-shrink: 0;
  border: 1px solid var(--line-green);
  border-radius: 4px;
  width: 20px;
  height: 20px;
  color: #000;
  appearance: none;
  outline: 0;
  cursor: pointer;
  transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
}
.sort-checkbox input[type=checkbox]::before {
  position: absolute;
  content: "";
  display: block;
  top: 3px;
  left: 7px;
  width: 5px;
  height: 9px;
  border-style: solid;
  border-color: #fff;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  opacity: 0;
}
.sort-checkbox input[type=checkbox]:checked {
  color: #fff;
  border-color: var(--main-color);
  background: var(--main-color);
}
.sort-checkbox input[type=checkbox]:checked::before {
  opacity: 1;
}
.sort-checkbox label {
  position: relative;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: var(--text-color);
}
.sort-checkbox label::before {
  position: absolute;
  content: attr(data-content);
}
.sort-checkbox.white input[type=checkbox] {
  border: 1px solid var(--white);
}
.sort-checkbox.white input[type=checkbox]::before {
  border-color: var(--main-color);
}
.sort-checkbox.white input[type=checkbox]:checked {
  color: var(--main-color);
  border-color: var(--white);
  background: var(--white);
}
.sort-checkbox.white label {
  color: var(--white);
}

.input-radio {
  display: flex;
  gap: 12px;
  align-items: center;
}
.input-radio input[type=radio] {
  position: relative;
  flex-shrink: 0;
  border: 1px solid var(--text-color);
  outline: none;
  opacity: 0.5;
  border-radius: 12px;
  height: 20px;
  width: 20px;
  color: #000;
  appearance: none;
  cursor: pointer;
  transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
}
.input-radio input[type=radio]::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  width: 12px;
  height: 12px;
  background: transparent;
  border-radius: 50%;
  pointer-events: none;
  transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
}
.input-radio input[type=radio]:checked {
  opacity: 1;
  border-color: var(--main-color);
}
.input-radio input[type=radio]:checked::before {
  background: var(--main-color);
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
}
.input-radio label {
  position: relative;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;
  color: var(--text-color);
  opacity: 0.7;
}
.input-radio label::before {
  position: absolute;
  content: attr(data-content);
  color: var(--main-color);
  opacity: 0;
}
.input-radio.white input[type=radio] {
  border: 1px solid var(--white);
}
.input-radio.white input[type=radio]:checked {
  border-color: var(--white);
}
.input-radio.white input[type=radio]:checked::before {
  background: var(--white);
}
.input-radio.white label {
  color: var(--white);
}

/* range */
input[type=range] {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 4px;
  background: var(--color-svetlo-zelenyy-1);
  border-radius: 20px;
  outline: none;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 17px;
  height: 17px;
  border-radius: 100%;
  background: var(--color-zelenyy);
  cursor: pointer;
}
input[type=range]::-moz-range-thumb {
  width: 17px;
  height: 17px;
  border-radius: 100%;
  background: var(--color-zelenyy);
  cursor: pointer;
}
input[type=range]:focus::-webkit-slider-thumb {
  outline: 2px solid var(--color-zelenyy);
  outline-offset: 0.125rem;
}
input[type=range]:focus::-moz-range-thumb {
  outline: 2px solid var(--color-zelenyy);
  outline-offset: 0.125rem;
}
input[type=range]:focus::-webkit-slider-thumb {
  outline: 2px solid var(--color-zelenyy);
  outline-offset: 0.125rem;
}

/* accordion */
.accordion {
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
  cursor: pointer;
  width: 100%;
  position: relative;
}
.accordion--active .accordion__btn-more {
  transform: rotate(180deg);
}
.accordion__content {
  max-height: 0;
  overflow: hidden;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

/* tabs */
.tab__btns-select {
  border: 1px solid var(--line-green);
  border-radius: 16px;
  padding: 10px;
  display: flex;
  gap: 10px;
  margin-bottom: 40px;
}

.tab__btn-select {
  flex-grow: 1;
  border-radius: 12px;
  padding: 12px 16px;
  background: var(--background-green);
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: var(--text-color);
  transition: all 0.3s ease;
}
.tab__btn-select:hover, .tab__btn-select.active {
  background: var(--green-200);
}

.tab-content {
  display: none;
}
.tab-content.active {
  display: block;
  width: 100%;
  min-height: 100%;
}

@media (max-width: 767px) {
  .tab-select {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
  .tab-select .tab-content {
    padding: 16px 0;
  }
  .tab__btns-select {
    display: contents;
  }
  .tab__btn-select {
    width: 100%;
    padding: 16px;
    display: flex;
    justify-content: space-between;
    gap: 10px;
    text-align: left;
  }
  .tab__btn-select::after {
    content: "";
    display: block;
    width: 8px;
    height: 8px;
    border-bottom: 1.5px solid var(--text-color);
    border-right: 1.5px solid var(--text-color);
    transform: rotate(45deg);
    position: relative;
    top: 5px;
    flex-shrink: 0;
    transition: all 0.2s ease-in;
  }
  .tab__btn-select.active::after {
    transform: rotate(225deg);
    top: 7px;
  }
}
/* rating */
.full-stars {
  text-align: center;
}

.full-stars .rating-group {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.full-stars input {
  position: absolute;
  left: -9999px;
}

.full-stars label {
  margin: 0;
  cursor: pointer;
}

.full-stars label svg {
  margin: 0 2px;
  height: 18px;
  width: 20px;
  fill: var(--color-temno-zelenyy);
  -webkit-transition: fill 0.3s;
  -o-transition: fill 0.3s;
  transition: fill 0.3s;
}

.full-stars input:checked ~ label svg {
  fill: transparent;
}

.full-stars .rating-group:hover label svg {
