
// propose
.propose {
    padding: 40px;
    background: url("./../img/forms/form-bg-2.jpg") no-repeat center / cover;

    &__wrap {
        padding: 36px 70px 40px 30px;
        border-radius: 38px;
        background: var(--white);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        gap: 80px;
        width: 50%;
        position: relative;
        z-index: 2;
    }

    &__img {
        position: absolute;
        bottom: 0;
        left: 50%;
        min-height: 90%;
        height: 90%;
    }
}

@media (max-width: 1470px) {
    .propose {
        padding: 20px;
        padding-bottom: 500px;
        &__wrap {
            width: 100%;
            gap: 25px;
        }

        &__img {
            position: absolute;
            bottom: 0;
            left: 50%;
            min-height: auto;
            height: auto;
            transform: translateX(-50%);
            width: 100%;
            max-width: 800px;
        }
    }
}
@media (max-width: 767px) {
    .propose {
        border-radius: 20px;
        padding: 24px 16px;
        background: var(--background-green);

        &__wrap {
            padding: 0;
            border-radius: 0;
            align-items: stretch;
            gap: 20px;
            background: transparent;
            & .link {
                background: var(--white);
            }
        }
        &__img {
            display: none;
        }
    }
}