
// location
.location {
    &__row {
        display: flex;
        justify-content: space-between;
        gap: 60px;
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 30px;

        &-plase {
            border-radius: 12px;
            padding: 20px;
            background: var(--background-green);
            font-size: 16px;
            display: flex;
            gap: 12px;
            &::before {
                content: "";
                display: block;
                width: 14px;
                height: 16px;
                background: url("./../img/icons/location.svg") no-repeat center / contain;
                flex-shrink: 0;
            }
        }

        &-near {
            font-size: 16px;
            & ul {
                display: flex;
                flex-wrap: wrap;
                gap: 10px;
                margin-top: 12px;
                & li {
                    border-radius: 10px;
                    padding: 12px 16px;
                    background: var(--background-green);
                    color: var(--text-color);
                }
            }
        }

        &-item {
            font-size: 16px;
            line-height: 130%;
            color: var(--text-color);

            &:not(:last-child) {
                padding-bottom: 20px;
                border-bottom: 1px solid var(--line-green);
                margin-bottom: 20px;
            }
        }

        &-transport {
            color: var(--secondary-color);
            margin-bottom: 12px;
            display: flex;
            align-items: center;
            gap: 12px;
            &::before {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                flex-shrink: 0;
            }
            &.car::before {
                background: url("./../img/icons/car.svg") no-repeat center / contain;
            }
            &.bus::before {
                background: url("./../img/icons/bus.svg") no-repeat center / contain;
            }
            &.train::before {
                background: url("./../img/icons/train.svg") no-repeat center / contain;
            }
        }
    }

    &__maps {
        border-radius: 24px;
        width: 100%;
        height: 100%;
        min-height: 505px;
        position: relative;
        overflow: hidden;
        flex-basis: 50%;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }
}

@media (max-width: 1024px) {
    .location {
        &__row {
            flex-direction: column;
            gap: 40px;
        }
    
        &__maps {
            flex-basis: 100%;
        }
    }
}
@media (max-width: 767px) {
    .location {
        &__row {
            gap: 24px;
        }
    
        &__info {
            gap: 24px;
    
            &-plase {
                padding: 12px;
                font-size: 12px;
            }
    
            &-near {
                font-size: 14px;
                & ul {
                    gap: 8px;
                    & li {
                        border-radius: 8px;
                        padding: 12px 14px;
                    }
                }
            }
    
            &-item {
                font-size: 14px;
                &:not(:last-child) {
                    padding-bottom: 16px;
                    margin-bottom: 16px;
                }
            }
    
            &-transport {
                margin-bottom: 10px;
                gap: 8px;
                &::before {
                    width: 18px;
                    height: 18px;
                }
            }
        }
    
        &__maps {
            border-radius: 16px;
            min-height: 340px;
        }
    }
}