
// doctors
.doctors {
    overflow: hidden;

    &__swiper {
        overflow: visible;
    }

    &__swiper-slide {
        height: auto;
    }

    &__card {
        padding: 10px 10px 30px;
        border-radius: 30px;
        background: var(--background-green);
        display: flex;
        flex-direction: column;
        gap: 26px;
        height: 100%;

        &-top {
            border-radius: 28px;
            background: var(--white);
            position: relative;
        }

        &-img {
            & img {
                width: auto;
                height: 380px;
                object-position: top;
                margin: 0 auto;
            }
        }

        &-exp {
            border-radius: 10px;
            padding: 12px;
            background: var(--main-color);
            font-size: 18px;
            line-height: 120%;
            color: var(--white);
            position: absolute;
            left: 20px;
            bottom: 20px;
        }

        &-main {
            padding: 0 15px;
            flex-grow: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            color: rgba(45, 49, 48, 0.7);
        }

        &-text {
            font-size: 18px;
            margin-bottom: 14px;
        }

        &-name {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 130%;
            letter-spacing: -0.01em;
            color: var(--main-color);
            margin-bottom: 12px;
        }

        &-links {
            border-radius: 16px;
            padding: 20px 24px;
            background: var(--white);
            & p {
                font-size: 14px;
                opacity: 0.6;
                margin-bottom: 12px;
            }
            & ul {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                gap: 10px;
                & a {
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 130%;
                    text-decoration: underline;
                    color: var(--secondary-color);
                }
            }
        }
    }
}

@media (max-width: 1260px) {
    .doctors {
        &__card {
            &-img {
                & img {
                    height: 340px;
                }
            }

            &-exp {
                font-size: 16px;
            }

            &-name {
                font-size: 22px;
            }

            &-links {
                padding: 14px 16px;
                & ul a {
                    font-size: 16px;
                }
            }
        }
    }
}
@media (max-width: 767px) {
    .doctors {
        &__card {
            padding: 8px 8px 16px;
            border-radius: 16px;
            gap: 16px;

            &-top {
                border-radius: 12px;
            }

            &-img {
                & img {
                    height: 274px;
                }
            }

            &-exp {
                border-radius: 6px;
                padding: 8px 10px;
                font-size: 12px;
                left: 10px;
                bottom: 10px;
            }

            &-main {
                padding: 0 8px;
            }

            &-text {
                font-size: 14px;
                margin-bottom: 20px;
            }

            &-name {
                font-size: 18px;
                margin-bottom: 10px;
            }

            &-links {
                border-radius: 10px;
                padding: 12px 10px 12px 12px;
                & ul {
                    & a {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}