
// reviews
.reviews {
    overflow: hidden;

    &__swiper {
        overflow: visible;
    }

    &__swiper-slide {
        height: auto;
    }

    &__card {
        padding: 30px;
        border-radius: 30px;
        background: var(--background-green);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        height: 100%;

        &-top {
            border: 1px solid var(--line-green);
            border-radius: 12px;
            padding: 20px 16px 20px 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 14px;
            margin-bottom: 20px;
        }

        &-estimation {
            display: flex;
            align-items: center;
            gap: 8px;
            color: var(--main-color);
        }

        &-stars {
            display: flex;
            gap: 4px;
        }

        &-logo img {
            max-height: 26px;
        }

        &-name {
            color: var(--text-color);
            margin-bottom: 12px;
        }

        &-text {
            font-weight: 400;
            font-size: 18px;
            line-height: 130%;
            color: var(--text-color);
            margin-bottom: 10px;
            max-height: 330px;
            overflow: hidden;
            transition: all 0.3s ease;
        }

        &-date {
            border-radius: 12px;
            padding: 20px;
            background: var(--white);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            color: var(--text-color);
            opacity: 0.6;
        }
    }
}

.reviews-page {
    &__swiper {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;

        & .swiper-wrapper {
            display: contents;
        }
        & .swiper-slide {
            display: contents;
        }
        & .swiper-pagination {
            display: none !important;
        }
    }
    &__btn {
        margin: 40px auto 0;
        min-width: 300px;
    }
}

@media (max-width: 1460px) {
    .reviews-page {
        &__swiper {
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 1200px) {
    .reviews {
        &__card {
            padding: 16px;
            &-wrap {
                padding: 0 6px;
            }
        }
    }
}
@media (max-width: 1100px) {
    .reviews-page {
        &__swiper {
            grid-template-columns: repeat(2, 1fr);
            gap: 16px;
        }
    }
}
@media (max-width: 767px) {
    .reviews {
        &__card {
            border-radius: 16px;
            padding: 8px;
            gap: 24px;
    
            &-top {
                border-radius: 10px;
                margin-bottom: 18px;
            }
    
            &-estimation {
                font-size: 14px;
                gap: 4px;
            }
    
            &-stars {
                gap: 2px;
            }
    
            &-logo img {
                max-height: 21px;
            }

            &-wrap {
                padding: 0 8px;
            }
    
            &-name {
                margin-bottom: 6px;
            }
    
            &-text {
                font-size: 14px;
                margin-bottom: 6px;
                max-height: 293px;
            }
    
            &-date {
                border-radius: 10px;
                padding: 18px 16px;
                font-size: 14px;
            }
        }
    }
}
@media (max-width: 700px) {
    .reviews-page {
        &__swiper {
            display: block;
            overflow: visible;
            & .swiper-wrapper {
                display: flex;
            }
            & .swiper-slide {
                display: block;
                height: auto;
            }
            & .reviews__card {
                display: flex !important;
            }
            & .reviews-yandex__card {
                display: block !important;
            }
            & .swiper-pagination {
                display: block !important;
            }
        }
        &__btn {
            display: none !important;
        }
    }
}


// rating
.rating {
    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__card {
        padding: 20px 20px 26px;
        border: 1px solid var(--line-green);
        border-radius: 30px;

        &-top {
            border-radius: 12px;
            background: var(--background-green);
            margin-bottom: 53px;
            height: 90px;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        &-logo img {
            max-height: 36px;
        }

        &-bottom {
            padding: 0 10px;
            display: flex;
            align-items: center;
            gap: 16px;
        }

        &-review {
            opacity: 0.7;
            padding-right: 16px;
            border-right: 1px solid var(--line-green);
            display: flex;
            justify-content: center;
            flex: 1 0 40%;
        }

        &-estimation {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 12px;
            font-size: 24px;
            line-height: 130%;
            color: var(--text-color);
            flex: 1 0 40%;
        }

        &-stars {
            display: flex;
            gap: 4px;
        }
    }
}

@media (max-width: 1370px) {
    .rating {
        &__cards {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}

@media (max-width: 767px) {
    .rating {
        &__cards {
            gap: 8px;
        }
    
        &__card {
            padding: 8px 8px 20px;
            border-radius: 12px;
    
            &-top {
                border-radius: 10px;
                margin-bottom: 20px;
                height: 60px;
            }
    
            &-logo img {
                max-height: 25px;
            }
    
            &-bottom {
                padding: 0 12px;
            }
            
            &-estimation {
                font-size: 14px;
                gap: 8px;
            }
    
            &-stars {
                gap: 2px;
            }
        }
    }
}
@media (max-width: 600px) {
    .rating {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 8px;
        }
    }
}


.reviews-yandex {
    overflow: hidden;
    &__swiper {
        overflow: visible;
    }
    &__card {
        display: block;
        border: 1px solid var(--line-green);
        border-radius: 24px;
        padding: 20px 6px;
        overflow: hidden;
        position: relative;
        &-img img {
            border-radius: 12px;
            max-height: 345px;
            min-height: 330px;
            object-position: top;
            object-fit: contain;
        }
    }

    &__swiper-pagination {
        display: block;
    }
}

@media (max-width: 767px) {
    .reviews-yandex {
        &__card {
            padding: 16px 6px 20px;
            border-radius: 16px;
            &-img img {
                max-height: 300px;
                min-height: 200px;
            }
        }
    }
}