.popup-change {
    &__content {
        padding: 60px;
        border-radius: 30px;
        background: var(--main-color);
        display: flex;
        justify-content: space-between;
        gap: 80px;
        color: var(--white);
    }

    &__left {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        flex-basis: 60%;
    }

    &__title {
        color: var(--white);
    }
}




.popup-response {
    &__content {
        max-width: 606px;
    }
    &__wrapper {
        text-align: center;
        color: rgba(255, 255, 255, 0.7);
        margin: 0 auto;
        & h3 {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 32px;
            line-height: 120%;
            letter-spacing: -0.01em;
            color: var(--white);
            margin-bottom: 30px;
        }
    }
}



// media popup-change-1
@media (max-width: 810px) {
    .popup-change {
        &__content {
            border-radius: 20px;
            padding: 30px 16px;
            flex-direction: column;
            gap: 46px;
        }
        &__left {
            flex-basis: 100%;
        }
        &__title {
            font-size: 18px;
            font-size: 24px;
        }
        &__img {
            display: none;
        }
    }

    .popup-change-response {
        &__content {
            max-width: 400px;
            padding: 10px;
            display: flex;
            flex-direction: column;
        }
        &__wrapper {
            & h3 {
                border-radius: 5px;
                padding: 16px 10px;
                font-size: 30px;
            }
            & p {
                gap: 14px;
                padding: 0 10px 20px;
            }
        }
        &__btn {
            padding-top: 20px;
            margin: 0 20px 20px;
        }
    }
}
