
// excursion
.excursion {
    padding: 40px;
    background: url("./../img/forms/form-bg.jpg") no-repeat center / cover;

    &__wrap {
        padding: 30px 70px 50px 40px;
        border-radius: 38px;
        background: var(--main-color);
        color: var(--white);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        gap: 80px;
        width: 50%;
        position: relative;
        z-index: 2;
    }

    &__title {
        color: var(--white);
        margin-bottom: 32px;
    }

    &__img {
        position: absolute;
        bottom: 0;
        left: 50%;
        min-height: 90%;
        height: 100%;
    }
}

@media (max-width: 1100px) {
    .excursion {
        padding: 20px;
        padding-bottom: 380px;
        &__wrap {
            width: 100%;
        }

        &__img {
            position: absolute;
            bottom: 0;
            left: 50%;
            min-height: auto;
            height: auto;
            transform: translateX(-50%);
            width: 100%;
            max-width: 500px;
        }
    }
}
@media (max-width: 767px) {
    .excursion {
        padding: 20px 16px;
        padding-bottom: 255px;
        background: var(--main-color);

        &__wrap {
            padding: 0;
            border-radius: 0;
            align-items: stretch;
            gap: 40px;
        }

        &__title {
            margin-bottom: 12px;
        }
        &__img {
            max-width: 370px;
        }
    }

}